.content-list {
  margin-bottom: 8rem;
  font-size: 1.8rem;
  line-height: 2.7rem;
  color: $black;

  @include mobile {
    margin-bottom: 4rem;
    font-size: 1.4rem;
    line-height: 1.8rem;
  }

  p {
    margin: 1rem 0;
  }

  a {
    color: #58B5E7;
    display: inline;
    text-decoration: underline;
    transition: color .3s;
    
    @include hover {
      color: $green;
    }
  }

  h3 {
    font-weight: bold;
    margin-bottom: 2rem;
    font-size: 2rem;
    line-height: 3rem;

    @include mobile {
      margin-bottom: 1rem;
    }
  }

  ul {
    margin: -0.5rem;
  }

  ul li {
    padding: 0.5rem;

    ul li ul {
      padding: 1rem 0;
    }

    ul li ul li {
      position: relative;    
      padding-left: 3.5rem;

      @include mobile {
        padding-left: 2.5rem;
      } 

      &:before {
        display: block;
        content: "";
        position: absolute;
        left: .5rem;
        top: 2rem;
        height: 0.2rem;
        width: 2rem;
        background: $black;

        @include mobile {
          width: 1rem;
          height: 0.1rem;
          top: 1.45rem;
        }
      }
    }
  }

  ol {
    margin: -0.7rem;
    counter-reset: numberCounter;
    padding: 1rem 0;
  }

  ol li {
    padding: 0.7rem;
    counter-increment: numberCounter;
  }

  ol li {
    &:before {
      display: inline;
      content: counter(numberCounter) ". ";
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;

    @include smallDesktop {
      display: block;
    }
  }

  &__li {
    flex: 1;

    &--left-col {
      padding-right: 6rem !important;

      @include smallDesktop {
        padding-right: 0 !important;
        margin-bottom: 3rem;
        width: 100%;
      }

      @include mobile {
        margin-bottom: 1.5rem;
      }
    }

    &--right-col {
      @include smallDesktop {
        width: 100%;
      }
    }
  }

  &__text-list {
    margin: -3.5rem !important;

    @include mobile {
      margin: -1.5rem !important;
    }
  }

  &__text-li {
    padding: 3.5rem !important;

    @include mobile {
      padding: 1.5rem !important;
    }
  }
}