.sale {
  background: $white;
  padding-bottom: 11.8rem;

  &__list-col {
    margin-bottom: 8rem;

    @include mobile {
      margin-bottom: 4rem;
    }
  }

  &__list {
    margin: -2.5rem;
  }

  &__li {
    padding: 2.5rem;
  }

  &__bottom {
    padding-top: 3rem;
    border-top: 1px solid $primaryText;

    @include mobile {
      padding-top: 1.5rem;
    }
  }

  &__btn {
    width: 19.6rem;
    height: 3.7rem;
    font-size: 1.3rem;
    line-height: 2rem;
    font-weight: bold;
    margin:  0 auto;
    border-radius: 18.5px;
  }
}
