.bank {
  $self: &;
  background: linear-gradient(135.69deg, #72E555 2.4%, #4DA636 76.86%);
  padding: 1.5rem;
  color: $white;
  border-radius: 5px;
  max-width: 20.4rem;

  @include mobile {
    max-width: 100%;
  }

  &__header {
    font-size: 1.3rem;
    line-height: 1.6rem;
    font-weight: bold;
    margin-bottom: 1.9rem;
  }

  &__list-col {
    margin-bottom: 1.6rem;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: -.6rem;
  }

  &__li {
    padding: .6rem;

    &--card {
      width: 12.7rem;
    }

    &--price {
      flex: 1;
    }
  }

  .field {
    $field: &;
    font-size: 1.1rem;
    transition: border-color .3s;

    &.parsley-error {
      border-color: #FC4040;
    }
  }

  .parsley-errors-list {
    display: none;
  }

  .btn {
    height: 3.7rem;
    border-radius: 1rem;
  }
}
