.news-slider {
  &__slider {
    position: relative;
  }

  &__btn {
    font-size: 5rem;
    line-height: 0;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 50;
    color: rgba($primaryText, .35);
    transition: color .3s;

    @include mobile {
      font-size: 2.5rem;
      display: none!important;
    }

    @include hover {
      color: $green;
    }

    &--prev {
      left: 0;
    }

    &--next {
      right: 0;
    }
  }

  .slick-dots {
    display: none!important;

    @include mobile {
      display: flex!important;
    }

    .slick-active button {
      background: $primaryText;
    }

    button {
      background: rgba($primaryText, .6);
    }
  }
}
