.licence {
  background: $white;
  padding-bottom: 24.5rem;

  @include mobile {
    padding-bottom: 6rem;
  }

  &__header {
    font-size: 3.6rem;
    line-height: 4.4rem;
    color: $black;
    font-weight: bold;
    margin-bottom: 3rem;

    @include mobile {
      font-size: 2.4rem;
      line-height: 2.8rem;
      margin-bottom: 1.5rem;
    }
  }

  &__desc {
    font-size: 2rem;
    line-height: 3rem;
    color: $primaryText;
    margin-bottom: 7.8rem;
    max-width: 88rem;

    @include mobile {
      font-size: 1.4rem;
      line-height: 1.8rem;
      margin-bottom: 3rem;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    margin: -3rem;

    @include smallDesktop {
      margin: -1.5rem;
    }
  }

  &__li {
    padding: 3rem;
    width: calc(100% / 3);

    @include smallDesktop {
      padding: 1.5rem;
      width: calc(100% / 2);
    }

    @include mobile {
    }
  }
}
