.business {
  position: relative;

  &__wrapper {
    padding: 21rem 28.5rem 8.9rem;

    @include desktop {
      padding: 11rem 0rem 8.9rem;
    }

    @include mobile {
      padding: 5rem 1rem 5rem 1rem;
    }
  }

  &__picture {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
  }

  &__container {
    position: relative;
    z-index: 5;
  }

  &__header {
    color: $white;
    font-size: 5rem;
    line-height: 6rem;
    font-weight: 700;
    @include inter;

    @include lessVerticalTablet {
      font-size: 4rem;
      line-height: 5rem;
    }

    @include smallMobile {
      font-size: $mobileMainHeader;
      line-height: 2.4rem;
    }
  }

  .user-text p {
    color: $white;
    font-size: 2.9rem;
    line-height: 3.7rem;
    font-weight: 500;
    @include inter;
    padding-top: 1.7rem;
    max-width: 600px;

    @include lessVerticalTablet {
      font-size: 2rem;
      line-height: 3rem;
    }

    @include smallMobile {
      font-size: $mobileText;
      line-height: 2.1rem;
    }
  }
}
