.order-form {
  $self: &;
  box-shadow: 0px 23px 25px rgba(0, 0, 0, 0.1);
  border-radius: 0px 0px 15px 15px;
  padding: 1.9rem 2.4rem .5rem;
  background: $white;
  display: flex;
  flex-wrap: wrap;
  max-width: 30rem;

  @include smallDesktop {
    padding: .6rem 1.2rem .2rem;
    max-width: 20rem;
  }

  @include mobile {
    max-width: 100%;
    background: $white;
    padding: 4rem 1.2rem 2rem;
    overflow: auto;
    overflow-x: hidden;
    max-height: 100vh;
    border-radius: 0;
  }

  &.basket-active.active {
    max-width: 82.1rem;

    @include mobile {
      max-width: 100%;
    }
  }

  &__basket {
    flex: 1;
    margin-right: 5.5rem;
    display: none;

    #{$self}.basket-active.active & {
      display: block;
    }

    @include mobile {
      margin-right: 0;
      margin-bottom: 3rem;
    }
  }

  &__content-col {
    width: 30rem;

    @include smallDesktop {
      width: 20rem;
    }

    @include mobile {
      width: 100%;
    }
  }

  &__head {
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    @include smallDesktop {
      display: block;
    }
  }

  &__header {
    font-size: 2.2rem;
    font-weight: bold;
    line-height: 2.7rem;
    color: $black;
    flex: 1;
    padding-right: 2.3rem;

    @include smallDesktop {
      font-size: 1.8rem;
      line-height: 2.2rem;
      flex: none;
      padding-right: 0;
    }
  }

  &__header-icon {
    font-size: 2.8rem;

    @include smallDesktop {
      display: none;
    }
  }

  &__content {
    margin-top: 1.2rem;
    margin-bottom: 1.2rem;
    display: none;

    @include smallDesktop {
      margin: .6rem 0;
    }

    #{$self}.active & {
      display: block;
    }
  }

  &__desc {
    font-size: 1.2rem;
    line-height: 1.8rem;
    color: $primaryText;

    @include smallDesktop {
      font-size: 1.2rem;
      line-height: 1.4rem;
    }
  }

  &__input-col {
    margin-top: 5.6rem;

    @include smallDesktop {
      margin-top: 1.2rem;
    }
  }

  &__input-list {
    margin: -1.2rem;

    @include smallDesktop {
      margin: -.6rem;
    }
  }

  &__input-li {
    padding: 1.2rem;

    @include smallDesktop {
      padding: .6rem;
    }
  }

  &__order-checkbox {
    margin-top: 1.6rem;

    @include smallDesktop {
      margin-top: .8rem;
    }
  }

  &__form-checkbox {
    margin-top: 1.6rem;

    @include smallDesktop {
      margin-top: .8rem;
    }
  }

  &__submit {
    margin-top: 3.1rem;

    @include smallDesktop {
      margin-top: 1.2rem;
    }
  }

  &__btn {
    width: 100%;
    text-align: center;
    display: block;
    font-size: 3rem;
    line-height: 0;
    margin-top: .5rem;

    @include smallDesktop {
      font-size: 1.8rem;
    }

    @include mobile {
      color: $primaryBackground;
      position: absolute;
      right: 1rem;
      top: 1rem;
      font-size: 2.5rem;
      width: auto;
    }
  }

  &__btn-icon {
    transition: transform .3s;

    #{$self}.active & {
      transform: rotate(-180deg);
    }

    @include mobile {
      display: none;
    }
  }

  &__btn-icon-close {
    display: none;

    @include mobile {
      display: block;
    }
  }

  &__field {
    @include smallDesktop {
      font-size: 1rem;
      height: 2.4rem;
      min-height: 2.4rem;
    }
  }
}
