.contact-form {
  box-shadow: 0 26px 52px rgba(32, 76, 89, 0.217002);
  background: $white;
  border-radius: 10px;
  padding: 4.3rem 6rem 5rem;
  color: $black;

  @include lessVerticalTablet {
    padding: 4rem 3rem 4rem;
  }

  &__head {
    display: flex;
    flex-wrap: wrap;
  }

  &__head-text-col {
    flex: 1;
    padding-left: 2rem;
    padding-top: 0.4rem;
  }

  &__icon {
    font-size: 6rem;
  }

  &__header {
    font-size: 3.6rem;
    line-height: 4.4rem;
    font-weight: bold;
    margin-bottom: 1rem;

    @include lessVerticalTablet {
      font-size: $mobileSecondHeader;
      line-height: 2rem;
    }
  }

  &__desc {
    font-size: 1.4rem;
    line-height: 1.7rem;
    color: $primaryText;
    margin-bottom: 5.7rem;

    @include lessVerticalTablet {
      font-size: $mobileText;
      line-height: 1.5rem;
      margin-bottom: 3rem;
    }
  }

  &__field-col {
    max-width: 85rem;
    margin-bottom: 8.2rem;
    display: flex;
    flex-wrap: wrap;

    @include mobile {
      margin-bottom: 2rem;
      display: block;
    }
  }

  &__msg-col {
    flex: 1;
    padding-left: 5.2rem;

    @include mobile {
      flex: none;
      padding-left: 0;
      margin-top: 2rem;
    }
  }

  &__field-list {
    margin: -1rem;
    width: 32rem;

    @include mobile {
      width: 100%;
    }
  }

  &__field-li {
    padding: 1rem;
  }

  &__field-msg.field--textarea {
    min-height: 12.5rem;
    height: 12.5rem;

    @include lessVerticalTablet {
      min-height: 8rem;
      height: 8rem;
    }
  }

  &__bottom {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  &__checkbox-col {
    width: 30rem;

    @include lessVerticalTablet {
      width: 100%;
    }
  }

  &__btn-col {
    flex: 1;
    padding-left: 5rem;

    @include lessVerticalTablet {
      padding-left: 0;
    }
  }

  &__submit {
    width: 22.2rem;

    @include lessVerticalTablet {
      width: 100%;
      max-width: 30rem;
      margin: 2rem auto 0 auto;
    }
  }
}
