.join {
  &__header {
    font-size: 2rem;
    line-height: 3rem;
    font-weight: bold;
    color: $black;
    margin-bottom: 6.2rem;

    @include mobile {
      margin-bottom: 2rem;
    }
  }

  &__content {
    padding-left: 29rem;

    @include smallDesktop {
      padding-left: 22.2rem;
    }

    @include mobile {
      padding-left: 0;
    }
  }

  &__list {
    margin: -1.5rem;
    position: relative;

    @include mobile {
      margin: -1rem;
    }

    &:before {
      display: block;
      content: '';
      position: absolute;
      left: -3.2rem;
      top: 1.5rem;
      bottom: 1.5rem;
      width: .3rem;
      background: #2175AA;

      @include mobile {
        display: none;
      }
    }
  }

  &__li {
    padding: 1.5rem;

    @include mobile {
      padding: 1rem;
    }
  }

  &__item {
    font-size: 2rem;
    line-height: 3rem;
    color: $black;

    @include mobile {
      font-size: 1.2rem;
      line-height: 1.5rem;
    }
  }
}
