.slick {
  &-dots {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    bottom: 0rem;
    left: 0;
    right: 0;

    li {
      margin-left: 1.5rem;

      &:first-child {
        margin-left: 0;
      }

      &.slick-active button {
        background: #fff;
      }
    }

    button {
      width: 1rem;
      height: 1rem;
      border-radius: 50%;
      background: rgba(#fff, .2);
      font-size: 0;
      line-height: 0;
      transition: background .3s;

      @include hover {
        background: $white;
      }
    }
  }
}
