.card-sales {
  border-radius: 1.5rem;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 1rem;
  color: $primaryText;
  display: flex;
  flex-wrap: flex-wrap;
  height: 100%;
  background: $primaryBackground;

  &__hover-img {
    display: block;
    flex: 1;
  }

  &__picture {
    height: 25.6rem;
    border-radius: 1rem;

    @include smallDesktop {
      height: 18rem;
    }

    @include mobile {
      width: 18rem;
      flex: none;
    }

    img {
      border-radius: 1rem;
    }
  }

  &__content-col {
    width: 22rem;
    padding-left: 2rem;
    padding-top: 1rem;

    @include mobile {
      width: auto;
      fleX: 1;
    }
  }

  &__header {
    font-size: 1.6rem;
    line-height: 2.4rem;
    font-weight: bold;
    margin-bottom: .9rem;

    @include mobile {
      font-size: 1.2rem;
      line-height: 1.6rem;
      margin-bottom: 1rem;
    }
  }

  &__desc {
    font-size: 1.2rem;
    line-height: 1.8rem;
    margin-bottom: 2rem;

    @include mobile {
      font-size: 1rem;
      line-height: 1.5rem;
      margin-bottom: 2rem;
    }
  }

  &__btn {
    height: 4rem;
    width: 14rem;
    border-radius: 1rem;

    @include mobile {
      width: 11.9rem;
    }
  }
}
