quote {
  display: block;
  padding-left: 9.2rem;
  font-size: 2.4rem;
  line-height: 3.6rem;
  color: #0056a2;
  margin-bottom: 5rem;

  @include lessVerticalTablet {
    font-size: $mobileSecondHeader;
    padding-left: 0;
    margin-bottom: 1.8rem;
  }

  p {
    padding-left: 3rem;
    position: relative;

    &:before {
      display: block;
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      width: 3px;
      background: #0056a2;
    }
  }
}
