.vote {
  $self: &;
  color: $white;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex: 1;

  &__header {
    font-size: 2.1rem;
    line-height: 2.5rem;
    font-weight: bold;
    margin-bottom: 3.3rem;

    @include smallDesktop {
      font-size: 1.8rem;
      line-height: 2.1rem;
    }
  }

  &__list-col {
    margin-bottom: 1.3rem;
  }

  &__list {
    margin: -.2rem;
  }

  &__li {
    padding: .2rem;
  }

  &__item {

  }

  &__input {
    display: none;
  }

  &__container {
    background: #36A5D1;
    transition: background .3s;
    padding: 1.3rem .9rem;
    border-radius: 4px;
    display: flex;
    align-items: center;

    @include hover {
      background: $green;
    }

    #{$self}__input:checked +  & {
      background: $green;

      .vote__icon {
        opacity: 1;
      }
    }
  }

  &__text {
    font-size: 1.2rem;
    line-height: 1.5rem;
    flex: 1;
    padding-right: 1rem;
  }

  &__icon {
    font-size: 1.3rem;
    opacity: 0;
    transition: opacity .3s;
  }

  &__desc {
    font-size: 1.1rem;
    line-height: 1.3rem;
    color: $white;
    text-align: center;
  }
}
