.tariff-detail {
  $self: &;
  margin: 10.9rem 0 5.7rem;

  @include mobile {
    margin: 5rem 0 2.5rem;
  }

  &__section-header {
    margin-bottom: 5.2rem;
  }

  &__tariff-header {
    font-size: 3.2rem;
    line-height: 3.9rem;
    color: $black;
    font-weight: bold;
    margin-bottom: 2.8rem;
  }

  &__list-col {
    width: calc(100% + 3rem);

    @include m1640 {
      padding: 0 5rem;
    };

    @include mobile {
      padding: 0;
      width: calc(100% + 1.5rem);
    };
  }

  &__list {
    @include mobile {
      padding-bottom: 3rem;
    };
  }

  &__li {
    width: 28rem;
    margin-right: 3rem;

    @include mobile {
      margin-right: 1.5rem;
    };
  }

  &__item {
    border-radius: 1rem;
    border: 1px solid rgba(32, 76, 89, 0.136336);
    border-width: 0 1px 1px 1px;
    padding: 0 2rem 1.6rem;
    background: $white;

    @include desktop {
      padding: 0 1rem 1.6rem;
    };

    @include lessFullHd {
      padding: 0 1rem 1.6rem;
    };
  }

  &__img-container {
    height: 22.3rem;
    margin-bottom: 1.7rem;
    margin-left: -2rem;
    width: calc(100% + 4rem);
    border-radius: 1rem 1rem 0 0;
    position: relative;

    @include lessFullHd {
      margin-left: -1rem;
      width: calc(100% + 2rem);
    };

    img {
      border-radius: 1rem 1rem 0 0;
    }
  }

  &__img-col {
    border-radius: 1rem 1rem 0 0;
    height: 100%;
  }

  &__img {
    font-size: 3.3rem;
    line-height: 1;
  }

  &__picture-icon {
    font-size: 3.3rem;
    line-height: 3.3rem;
    position: absolute;
    left: 2rem;
    top: 2rem;
    z-index: 5;
  }

  &__header {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    min-height: 3.4rem;
    margin-bottom: 1.5rem;
  }

  &__header-text {
    font-size: 1.8rem;
    line-height: 2.7rem;
    color: $primaryText;
    font-weight: 500;
    flex: 1;
    padding-right: 1.2rem;
  }

  &__price {
    font-size: 2.4rem;
    line-height: 2.6rem;
    color: $primaryText;
    font-weight: bold;
    margin-bottom: 2.8rem;

    &--tariff {
      margin-bottom: 2.1rem;
    }
  }

  &__desc {
    font-size: 1.6rem;
    line-height: 2.4rem;
    color: $primaryText;
    margin-bottom: 1.6rem;
  }

  .tariff-detail__btn {
    width: 100%;
    margin-bottom: 2.2rem;
    height: 4rem;
    background: #EFF3F9;
    padding: 0 1.6rem;

    .btn__text {
      padding-right: 1rem;
    }
  }

  &__service-btn {
    font-size: 1.3rem;
    line-height: 1.3rem;
    font-weight: 500;
    color: $primaryText;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    transition: color .3s;

    @include hover {
      color: $green;
    }
  }

  &__service-btn-text {
    margin-right: .5rem;
  }

  &__service-btn-icon {
    font-size: 1rem;
    transition: color .3s, transform .3s;

    #{$self}__services.active & {
      transform: rotate(-180deg);
      color: $green;
    }
  }

  &__content {
    display: none;
    margin-top: 2.5rem;


    #{$self}__services.active & {
      display: block;
    }
  }

  &__service-list {
    margin: -.7rem;
  }

  &__service-li {
    padding: .7rem;
  }

  &__service-card {
    padding: 1rem 1.4rem;
    border: 1px solid rgba(32, 76, 89, 0.136336);
    border-radius: 10px;
    color: $primaryText;
  }

  &__service-header {
    font-size: 1.3rem;
    line-height: 1.9rem;
    margin-bottom: 1.1rem;
    max-width: 15.2rem;
  }

  &__service-bottom {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  &__service-price {
    font-size: 1.4rem;
    line-height: 1.7rem;
    font-weight: bold;
    flex: 1;
    padding-right: 2rem;
  }

  &__close-col {
    text-align: center;
  }

  &__close {
    display: inline-block;
    font-size: 1.3rem;
    line-height: 2.2rem;
    color: $primaryText;
    text-align: center;
    margin-top: 2.2rem;
    transition: color .3s;

    @include hover {
      color: $green;
    }
  }

  &__popup-btn {
    font-size: 1.2rem;
    line-height: 1.8rem;
    color: #6D7D95;
    border-bottom: 1px dashed #6D7D95;
    display: inline-block;
    transition: color .3s, border-color .3s;

    @include hover {
      color: $green;
      border-color: $green;
    }
  }

  &__tab-col {
    margin-bottom: 5.2rem;
    padding-bottom: 3.3rem;
    border-bottom: 1px solid $black;

    @include mobile {
      padding-bottom: 2rem;
      border: none;
      margin-bottom: 2rem;
    };
  }

  &__tab-list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: -5rem;

    @include mobile {
      margin: -1rem;
    };
  }

  &__tab-li {
    padding: 5rem;

    @include mobile {
      padding: 1rem;
    };
  }

  &__tab-btn {
    font-size: 3rem;
    line-height: 3.6rem;
    font-weight: bold;
    color: rgba($black, .5);
    position: relative;
    transition: color .3s;

    @include mobile {
      font-size: 2rem;
      line-height: 2.4rem;
    };

    &:after {
      display: block;
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      bottom: -3.5rem;
      height: 3px;
      background: transparent;
      transition: background .3s;

      @include mobile {
        bottom: -1rem;
      };
    }

    #{$self}__tab-li.active &:after {
      background: #FF0000;
    }

    @include hover {
      color: $green;
    };

    #{$self}__tab-li.active & {
      color: $black;
    }
  }

  &__tab-content-li {
    opacity: 0;
    pointer-events: none;
    height: 0;

    &.active {
      opacity: 1;
      pointer-events: auto;
      height: auto;
    }
  }

  .arrow {
    @include m1640 {
      font-size: 4rem;
    };

    &--prev {
      margin-left: -2.2rem;
      transform: translate(-100%, -50%);

      @include m1640 {
        margin-left: 0;
        transform: translate(0, -50%);
        left: -5rem;
      };
    }

    &--next {
      right: -4.2rem;

      @include m1640 {
        right: -2.2rem;
      };
    }
  }

  .slick {
    &-dots {
      opacity: 0;
      pointer-events: none;

      @include mobile {
        opacity: 1;
        pointer-events: auto;
      };

      button {
        background: rgba($black, .2);
      }

      .slick-active {
        button {
          background: $black;
        }
      }
    }
  }
}
