.toggle {
  $self: &;
  width: 3.2rem;
  height: 2rem;
  background: $white;
  border-radius: 1.6rem;
  position: relative;
  border: 1px solid rgba($black, .1);
  transition: border-color .3s, background .3s, opacity .3s;

  &.active {
    background: $green;
  }

  &.disabled {
    pointer-events: none;
    opacity: .5;
  }

  &__circle {
    position: absolute;
    left: .2rem;
    top: 50%;
    transform: translateY(-50%);
    width: 1.4rem;
    height: 1.4rem;
    background: #12A1D9;
    border-radius: 50%;
    transition: background .3s, left .3s;

    #{$self}.active & {
      left: calc(100% - 1.6rem);
      background: $white;
    }
  }
}
