.slider-arrow {
  font-size: 5.2rem;
  line-height: 0;
  color: rgba($white, .35);
  transition: color .3s!important;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 50;

  &--prev {
    left: 0;
  }

  &--next {
    right: 0;
  }

  @include hover {
    color: $white;
  }

  &.slick-disabled {

  }
}
