.gallery-detail {
  $self: &;
  margin-bottom: 17.5rem;

  @include mobile {
    margin-bottom: 8rem;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    margin: -1.5rem;

    @include mobile {
      align-items: flex-start;
      align-content: flex-start;
    }
  }

  &__li {
    padding: 1.5rem;
    width: 27.75%;

    @include mobile {
      width: 100%;
      max-width: 18.7rem;
    }

    &--big {
      width: 50%;

      @include mobile {
        width: 100%;
        max-width: 37.7rem;
      }
    }

    &--medium {
      width: 44.5%;

      @include mobile {
        width: 100%;
        max-width: 37.7rem;
      }
    }
  }

  &__item {
    height: 100%;
    position: relative;
  }

  &__img-col {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
  }

  &__picture {
    fleX: 1;
    display: flex;
    width: 100%;
    height: 100%;
    border-radius: 1rem;
    overflow: hidden;

    @include mobile {
      height: 15.7rem;
    }

    #{$self}__li--big & {
      @include mobile {
        height: 25.5rem;
      }
    }

    #{$self}__li--medium & {
      @include mobile {
        height: 25.5rem;
      }
    }

    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
      border-radius: 1rem;
      transition: transform .3s;
    }
  }

  &__card-header {
    font-size: 1.4rem;
    line-height: 2.4rem;
    color: $white;
    font-weight: bold;
    @include inter;
    padding: 2rem 3.6rem;
    background: linear-gradient(0deg, rgba(32, 76, 89, 0.4), rgba(32, 76, 89, 0.4));
    border-radius: 1rem;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    opacity: 0;
    pointer-events: none;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    transition: opacity .3s;

    @include mobile {
      position: static;
      display: block;
      pointer-events: auto;
      opacity: 1;
      background: none;
      color: $primaryText;
      padding: 0;
      text-align: left;
      margin-top: .8rem;
      font-size: 1.2rem;
      line-height: 1.6rem;
    }

    #{$self}__picture:hover + & {
      @include fromDesktop {
        opacity: 1;
      }
    }

    #{$self}__li--big &, #{$self}__li--medium & {
      @include mobile {
        font-size: 1.4rem;
        line-height: 2.4rem;
      }
    }
  }

  &__card-header-text {
    max-width: 25rem;
    text-align: center;
  }
}
