.products {
  $self: &;
  margin-bottom: 23.8rem;

  @include mobile {
    margin-bottom: 10rem;
  }

  &__list-col {
    margin-bottom: 7.7rem;

    @include mobile {
      margin-bottom: 3rem;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    margin: -1.5rem;

    @include smallDesktop {
      margin: -.7rem;
    }
  }

  &__li {
    width: calc(100% / 3);
    padding: 1.5rem;

    @include smallDesktop {
      padding: .7rem;
      width: calc(100% / 2);
    }
  }

  &__item {
    background: $primaryBackground;
    border-radius: 15px;
    height: 100%;
    padding-top: 1rem;
  }

  &__img-col {
    height: 25.4rem;
    border-radius: 1rem;
    margin: 0 1rem 0 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: $white;
    margin-bottom: 3.7rem;

    @include mobile {
      height: 12rem;
      margin-bottom: 2rem;
    }

    &:hover {
      @include fromDesktop {
        img {

          &:hover {
            transform: scale3d(1.1, 1.1, 1.1);
          }
        }
      }
    }
  }

  &__icon {
    font-size: 13rem;
    color: rgba($primaryText, .3);

    @include mobile {
      font-size: 8rem;
    }
  }

  &__img {
    height: 100%;
    box-shadow: 0px 26px 52px rgba(32, 76, 89, 0.1);
    border-radius: 1rem;

    img {
      border-radius: 1rem;
      transition: transform .3s;
    }
  }

  &__header {
    display: block;
    font-size: 2rem;
    line-height: 3rem;
    font-weight: bold;
    color: $black;
    margin-bottom: 1rem;
    transition: color .3s;

    @include mobile {
      font-size: 1.4rem;
      line-height: 1.6rem;
    }

    @include hover {
      color: $green;
    }
  }

  &__desc {
    font-size: 1.8rem;
    line-height: 2.7rem;
    color: $primaryText;

    @include mobile {
      font-size: 1rem;
      line-height: 1.3rem;
    }
  }

  &__content {
    padding: 0 3.3rem 5.5rem;

    @include mobile {
      padding: 0 1.5rem 2rem;
    }
  }

  &__bottom-desc {
    font-size: 2rem;
    line-height: 3rem;
    color: $primaryText;

    @include mobile {
      font-size: 1.4rem;
      line-height: 1.8rem;
    }
  }

  &__tab-list-col {
    margin-bottom: 4.3rem;
    padding-bottom: 1rem;

    @include mobile {
      margin-bottom: 1rem;
      overflow: auto;
    }
  }

  &__category {
    font-size: 1.2rem;
    line-height: 2rem;
    color: rgba($primaryText, .8);
    margin-bottom: .5rem;

    @include mobile {
      font-size: 1rem;
      line-height: 1.3rem;
    }
  }

  &__link-list {

  }

  &__link-li {
    line-height: 0;
  }

  &__link {
    font-size: 1.8rem;
    line-height: 2.7rem;
    color: #2175AA;
    transition: color .3s;

    @include hover {
      color: $green;
    }

    @include mobile {
      font-size: 1rem;
      line-height: 2rem;
    }
  }

  &__tab-content {

  }

  &__tab-item {
    display: none;

    &.active {
      display: block;
    }
  }
}
