.text-block {
  background: $primaryBackground;
  padding-top: 2.2rem;
  padding-bottom: 8.8rem;

  @include mobile {
    padding-top: 1.5rem;
    padding-bottom: 3.9rem;
  }

  &__content {
    box-shadow: 0 26px 52px rgba(32, 76, 89, 0.217002);
    background: $primaryText;
    max-width: 63rem;
    position: relative;
    padding: 4.2rem 7rem 4.7rem;
    color: $white;
    border-radius: .8rem;
    margin: 0 auto;

    @include mobile {
      max-width: 34.5rem;
      padding: 2rem 2rem 2.8rem 2rem;
    }

    &:before {
      display: block;
      content: '';
      width: 3rem;
      height: 1rem;
      background: url(../img/css/text-block-triangle.png) no-repeat center;
      background-size: 100% auto;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      bottom: 100%;
    }
  }

  &__header {
    font-size: 2.9rem;
    line-height: 3.5rem;
    font-weight: bold;
    margin-bottom: 1.9rem;
    text-align: center;

    @include mobile {
      font-size: 1.8rem;
      line-height: 2.7rem;
      margin-bottom: 1rem;
    }
  }

  &__header-desc {
    font-size: 1.4rem;
    @include sourceSans;
    text-align: center;

    @include mobile {
      font-size: 1.2rem;
      line-height: 1.6rem;
      padding: 0 2rem;
    }
  }

  &__field {
    margin-top: 3.7rem;

    @include mobile {
      margin-top: 1.4rem;
    }
  }

  &__connect-text {
    font-size: 1.6rem;
    line-height: 2.6rem;
    text-align: center;
    margin-top: 1.9rem;
  }

  &__link {
    transition: color .3s;

    @include hover {
      color: $green;
    }

    &--bold {
      font-weight: bold;
    }
  }
}
