.main-slider {
  color: $white;

  &__img-col {
    position: absolute;
    right: .5rem;
    width: 68.4rem;
    height: 46.5rem;
    height: 100%;
    top: 0;

    @include smallDesktop {
      right: 10rem;
      width: 40rem;
      height: 30rem;
    }

    @include mobile {
      right: -1.5rem;
      top: -4rem;
      width: 19.5rem;
      height: 14rem;
    }
  }

  &__img {

  }

  &__fullwidth-img {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
  }

  &__fullwidth-picture {
    width: 100%;
    height: 100%;
    pointer-events: none;
  }

  &__container {
    padding-top: 1.2rem;
    padding-bottom: 3.3rem;

    @include mobile {
      padding-bottom: 3.4rem;
      padding-top: 5.6rem;
    }
  }

  &__content-col {
    max-width: 48.6rem;

    @include smallDesktop {
      max-width: 36.6rem;
    }

    @include mobile {
      max-width: 16.3rem;
    }
  }

  &__slider {
    position: relative;
  }

  &__slide-col {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    height: 40.5rem;
    background: no-repeat right 6.9rem center;
    background-size: auto 100%;
    position: relative;
    padding-top: 13rem;
    padding-left: 8rem;
    padding-right: 0rem;

    @include smallDesktop {
      height: auto;
      background-position: right center;
      padding: 7rem 0 7rem 8rem;
    }

    @include mobile {
      padding: 0;
    }
  }

  &__text-col {
    position: relative;
    z-index: 30;
  }

  &__title {
    font-size: 5rem;
    line-height: 6rem;
    font-weight: bold;
    margin-bottom: 1.8rem;

    @include smallDesktop {
      font-size: 4rem;
      line-height: 4.8rem;
      margin-bottom: .8rem;
    }

    @include mobile {
      font-size: 1.8rem;
      line-height: 2.2rem;
      margin-bottom: 1.5rem;
    }
  }

  &__desc {
    font-size: 1.2rem;
    line-height: 1.5rem;
    margin-bottom: .4rem;

    @include mobile {
      font-size: .8rem;
      line-height: 1rem;
    }
  }

  &__btn {
    width: 18rem;
    height: 5.2rem;
    font-size: 1.6rem;
    transition: background .3s, color .3s!important;

    @include mobile {
      width: 11.9rem;
      height: 3.6rem;
      font-size: 1.2rem;
    }
  }

  .slider-arrow {
    top: 44%;

    @include mobile {
      display: none!important;
    }
  }

  .swiper-pagination {
    bottom: 4.7rem;

    @include mobile {
      bottom: 1.5rem;
    }
  }
}
