.user-block {
  font-size: 1.8rem;
  line-height: 2.7rem;
  color: $black;

  p {
    margin: 1rem 0;
  }

  a {
    color: #58b5e7;
    display: inline;
    text-decoration: underline;
    transition: color 0.3s;

    @include hover {
      color: $green;
    }
  }

  h3 {
    font-weight: bold;
    margin-bottom: 2rem;
    font-size: 2rem;
    line-height: 3rem;

    @include mobile {
      margin-bottom: 1rem;
    }
  }

  ul {
    margin: -0.5rem;

    & > li {
      padding: 0.5rem;
      padding: 1rem 0;
      position: relative;
      padding-left: 3.5rem;

      @include mobile {
        padding-left: 2.5rem;
      }

      &:before {
        display: block;
        content: "";
        position: absolute;
        left: 0.5rem;
        top: 2.3rem;
        height: 0.2rem;
        width: 2rem;
        background: $black;

        @include mobile {
          width: 1rem;
          height: 0.1rem;
        }
      }
    }
  }

  ol {
    margin: -0.7rem;
    padding: 1rem 0;
    counter-reset: numberCounter;

    & > li {
      padding: 0.7rem;
      counter-increment: numberCounter;

      &:before {
        display: inline;
        content: counter(numberCounter) ". ";
      }
    }
  }

  figure {
    overflow-x: auto;
    display: block;
    width: auto;
  }

  table {
    width: 100%;
    border-collapse: collapse;
    min-width: 110.6rem;
    
    @include mobile {
      min-width: 110.6rem;
    }

    tr {
      &:first-child {
        td {
          border-bottom: 1px solid #204c59;
          font-size: 1.8rem;
          line-height: 2.2rem;
          color: #000;
          font-weight: bold;

          @include lessFullHd {
            font-size: 1.6rem;
            line-height: 2rem;
          }

          @include mobile {
            font-size: 1.4rem;
            line-height: 1.7rem;
            padding-bottom: 1.5rem;
          }
        }
      }
    }

    td {
      font-size: 1.4rem;
      line-height: 2.4rem;
      color: #3a5565;
      padding: 1.5rem 3rem 1.5rem 0;
      vertical-align: top;
      border-bottom: 1px solid #204c59;

      @include lessFullHd {
        font-size: 1.2rem;
        line-height: 1.9rem;
      }

      @include mobile {
        padding: 0.6rem 1.5rem 0.6rem 0;
      }

      &:last-child {
        padding-right: 0;
      }

      &:nth-child(2) {
        max-width: 17rem;

        @include lessFullHd {
          max-width: 15rem;
        }
      }

      &:nth-child(4) {
        max-width: 24rem;

        @include lessFullHd {
          max-width: 19rem;
        }
      }

      &:nth-child(5) {
        max-width: 25.6rem;

        @include lessFullHd {
          max-width: 23rem;
        }
      }

      &:nth-child(6) {
        max-width: 29.9rem;

        @include lessFullHd {
          max-width: 26rem;
        }
      }
    }
  }
}
