.internet {
  color: $black;
  margin-bottom: 14.3rem;
  $self: &;

  &__text-block {
    padding: 0!important;
    background: none!important;
    margin-bottom: 2rem;

    .text-block {
      &__container {
        padding: 0!important;
        max-width: 100%;
      }

      &__content {
        &:before {
          bottom: auto;
          top: 100%;
          transform: rotate(180deg) translateX(50%);
        }
      }
    }
  }

  &__header {
    font-size: 3.6rem;
    line-height: 4.4rem;
    margin-bottom: 3rem;

    @include mobile {
      font-size: 2.4rem;
      line-height: 2.8rem;
      margin-bottom: 1.5rem;
    }
  }

  &__desc {
    font-size: 2rem;
    line-height: 3rem;
    color: $primaryText;
    margin-bottom: 10.9rem;

    @include mobile {
      font-size: 1.4rem;
      line-height: 1.8rem;
      margin-bottom: 3rem;
    }
  }

  &__list-col {
    margin-bottom: 14rem;

    @include mobile {
      margin-bottom: 3rem;
    }
  }

  &__list {
    margin: -1.3rem;
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
  }

  &__li {
    padding: 1.3rem;
    width: calc(100% / 3);

    @include smallDesktop {
      width: calc(100% / 2);
    }

    @include mobile {
      width: 100%;
    }
  }

  &__card {
    height: 100%;
    min-height: 47.8rem;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: flex-end;
    padding: 0 3.9rem 4.6rem 5rem;
    position: relative;
    overflow: hidden;
    border-radius: 1rem;

    @include mobile {
      padding: 2rem 1.5rem;
      min-height: 30rem;
    }

    & > * {
      position: relative;
      z-index: 5;
    }
  }

  &__card-img {
    position: absolute!important;
    z-index: 0!important;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    border-radius: 1rem;

    img {
      border-radius: 1rem;
    }
  }

  &__card-header {
    font-size: 2rem;
    line-height: 3rem;
    font-weight: bold;
    margin-bottom: 1rem;

    @include mobile {
      font-size: 1.6rem;
      line-height: 2rem;
      margin-bottom: .5rem;
    }
  }

  &__card-desc {
    font-size: 1.8rem;
    line-height: 2.7rem;
    color: $primaryText;

    @include mobile {
      font-size: 1.4rem;
      line-height: 1.8rem;
    }
  }

  &__static-desc {
    margin-bottom: 3rem;

    @include mobile {
      margin-bottom: 2rem;
    }
  }

  &__link-list {
    margin: -1rem;
    display: flex;
    flex-wrap: wrap;
  }

  &__link-li {
    padding: 1rem;
  }

  &__get-link {
    padding: 0 .7rem;
    height: 3.7rem;
    text-align: center;
    border-radius: 18.5px;
    padding: 0 4.3rem;
  }
}
