.search-field {
  max-width: 53.2rem;

  &__input-col {
    position: relative;
    margin-bottom: 2.2rem;
  }

  &__input {
    width: 100%;
    height: 4.8rem;
    display: block;
    border-radius: 1rem;
    background: $white;
    font-size: 1.8rem;
    line-height: 2rem;
    color: $primaryText;
    padding: 0 6rem 0 1.7rem;

    @include mobile {
      font-size: 1.6rem;
      line-height: 1.8rem;
    }
  }

  &__submit {
    position: absolute;
    right: 1.7rem;
    top: 50%;
    transform: translateY(-50%);
    line-height: 0;
  }

  &__submit-icon {
    font-size: 1.6rem;
    color: #2175AA;
  }

  &__result {
    font-size: 1.6rem;
    line-height: 2.4rem;
    color: $white;
  }

  &__value {
    font-weight: bold;
  }
}
