.sub-tags {
  $self: &;
  margin: .6rem 0 0;

  @include mobile {
    margin-top: 2rem;
  }

  &__list {
    display: inline-flex;
    flex-wrap: wrap;
    border-top: 1px solid rgba($primaryText, .2);
    padding-top: 1rem;
    position: relative;

    @include mobile {
      margin: -1rem;
      border: none;

      &:before {
        display: block;
        content: '';
        position: absolute;
        top: 1rem;
        left: 1rem;
        right: 0;
        height: 1px;
        background: rgba($primaryText, .2);
      }
    }
  }

  &__li {
    margin-left: 2.5rem;

    &:first-child {
      margin-left: 0;
    }

    @include mobile {
      margin-left: 0;
      padding: 1rem;
    }
  }

  &__btn {
    display: block;
    font-size: 1.3rem;
    line-height: 1.6rem;
    color: #6D7D95;
    font-weight: 600;
    transition: color .3s;

    @include hover {
      color: $green;
    }

    #{$self}__li.active & {
      color: $lightBlue;
    }
  }
}
