.abonents {
  background: $white;
  margin: $mainPageSectionGap;
  $self: &;

  @include mobile {
    margin: 6rem 0;
  }

  &__section-header {
    margin-bottom: 4.4rem;

    @include mobile {
      margin-bottom: 3rem;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    margin: -.8rem;

    @include mobile {
      display: block;
    }
  }

  &__li {
    padding: .8rem;

    &--pay-var {
      flex: 1;
    }

    &--help {
      width: calc(100% / 4);

      @include mobile {
        width: 100%;
      }
    }

    &--vote {
      width: 25.6rem;

      @include mobile {
        width: 100%;
      };
    }

    &--faq-list {
      flex: 1;
    }
  }

  &__item {
    border-radius: 10px;
    background: $primaryBackground;
    padding: 2rem;
    height: 100%;
    display: flex;
    flex-direction: column;

    @include mobile {
      padding: 2rem 2.8rem;
    }

    #{$self}__li--pay-var & {
      background: $white;
      padding: 0;

      @include mobile {
        padding: 0;
      }
    }

    #{$self}__li--help & {
    }

    #{$self}__li--vote & {
      background: linear-gradient(136.55deg, #01B6F3 2.39%, #0587BD 78.69%);
      display: flex;
      flex-direction: column;
    }
  }

  &__header {
    font-size: 1.3rem;
    line-height: 1.6rem;
    color: $black;
    font-weight: bold;
    margin-bottom: 3.4rem;

    @include mobile {
      margin-bottom: 3rem;
    }

    #{$self}__li--vote & {
      color: $white;
    }
  }

  &__link-col {
    margin-bottom: 3.2rem;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link-list {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    margin: -1.2rem;

    @include mobile {
      margin: -1.5rem;
    }
  }

  &__link-li {
    padding: 1.2rem;

    @include mobile {
      padding: 1.5rem;
    }

    &--50 {
      width: 50%;

      @include mobile {
        width: 100%;
      }
    }
  }

  &__link-header {
    font-size: 1.2rem;
    line-height: 1.5rem;
    font-weight: 600;
    color: rgba($primaryText, .5);
    margin-bottom: 1.8rem;
  }

  &__info-list {
    margin: -.4rem;
  }

  &__info-li {
    padding: .4rem;
    line-height: 0;
  }

  &__link {
    font-size: 1.2rem;
    line-height: 1.8rem;
    color: #0056A2;
    transition: color .3s;

    @include hover {
      color: $green;
    }
  }

  &__login-form {
    margin-top: auto;
  }

  &__link-bot {
    display: inline-flex;
    align-items: center;
    margin-top: 1.7rem;
    font-size: 1.3rem;
    line-height: 1.9rem;
    color: #0056A2;
    transition: color .3s;

    @include hover {
      color: $green;
    }

    &--mobile {
      display: none;

      @include mobile {
        display: inline-flex;
      };
    }

    &--desktop {
      @include mobile {
        display: none;
      };
    }
  }

  &__link-bot-text {
    flex: 1;
    padding-right: .5rem;
  }

  &__link-bot-icon {
    font-size: .9rem;
  }
}
