.tab-switcher {
  $self: &;

  &__list {
    display: flex;
    align-items: center;
    margin: -.7rem;
    line-height: 0;
  }

  &__li {
    padding: .7rem;
    line-height: 0;
  }

  &__btn {
    line-height: 0;
  }

  &__background {
    transition: fill .3s;
    fill: $primaryBackground;

    #{$self}__li.active & {
      fill: $green;
    }

    #{$self}__li:hover & {
      @include fromDesktop {
        fill: $green;
      }
    }
  }

  &__icon {
    transition: fill .3s;
    fill: rgba($primaryText, .5);

    #{$self}__li.active & {
      fill: $white;
    }

    #{$self}__li:hover & {
      @include fromDesktop {
        fill: $white;
      }
    }
  }
}
