.benefits {
  padding: 12rem 0 16.4rem;
  background: $primaryBackground;

  @include lessVerticalTablet {
    padding: 5rem 0 5rem;
  }

  &__section-header {
    margin-bottom: 4rem;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin: -2.25rem;

    @include mobile {
      margin: -1.2rem;
    }
  }

  &__li {
    padding: 2.25rem;
    width: calc(100% / 4);

    @include mobile {
      padding: 1.2rem;
      width: calc(100% / 2);
    }
  }

  &__item {
    background-color: $white;
    padding: 5.5rem 3.5rem 4.8rem 3.5rem;
    border-radius: 1.5rem;
    text-align: center;
    width: 100%;
    height: 100%;

    @include mobile {
      padding: 1.5rem;
    }
  }

  &__icon {
    width: 8rem;
    height: 8rem;

    @include mobile {
      width: 4rem;
      height: 4rem;
    }
  }

  &__item-text {
    color: $primaryText;
    font-size: 1.8rem;
    line-height: 2.2rem;
    font-weight: 500;
    @include inter;
    margin-top: 6.3rem;

    @include lessVerticalTablet {
      margin-top: 1.2rem;
      font-size: $mobileText;
    }
  }
}
