.select {
  $self: &;
  position: relative;

  &__head {
    background: rgba($white, .79);;
    border-radius: 10px;
    padding: 1.2rem 2rem;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    transition: background .3s;
    pointer-events: none;

    @include hover {
      background: $green;
    }

    @include lessVerticalTablet {
      padding: 1rem 1.8rem;
    }
  }

  &__placeholder {
    flex: 1;
    font-size: 1.4rem;
    line-height: 2.1rem;
    color: $primaryText;
    padding-right: 2rem;
    text-align: left;
  }

  &__icon {
    font-size: 1rem;
    color: $primaryText;
    stroke-width: 1;
    transition: transform .3s;

    #{$self}.active & {
      transform: rotate(-180deg);
    }
  }

  &__dropdown {
    left: 0;
    top: 100%;
    right: 0;
    position: absolute;

    #{$self}.active & {
      display: block;
    }
  }

  &__list {
    background: $white;
    border-radius: 10px;
    position: absolute;
    transform: translateY(2px);
    opacity: 0;
    pointer-events: none;
    transition: opacity .3s;
    box-shadow: 0 0 10px 1px rgba($black, .2);

    &.active {
      opacity: 1;
      pointer-events: auto;
    }
  }

  &__li {

  }

  &__option {
    padding: 1rem 0;
    text-align: center;
    font-size: 1.4rem;
    color: $primaryText;
    cursor: pointer;
    transition: color .3s;

    @include hover {
      color: $green;
    }

    #{$self}__li:first-child & {
      border-radius: 10px 10px 0 0;
    }

    #{$self}__li:last-child & {
      border-radius: 0 0 10px 10px;
    }
  }
}
