.about {
  background: $white;

  &__head {
    padding: 5.4rem 0 6rem;
    background: $white;
    height: 51rem;
    position: relative;
    @include mobile {
      height: auto;
      padding: 3rem 0;
      margin-bottom: 3rem;
    }

    .container {
      & > * {
        position: relative;
        z-index: 5;
      }
    }
  }

  &__head-img {
    position: absolute!important;
    z-index: 0!important;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
  }

  &__header {
    font-size: 5rem;
    line-height: 6rem;
    font-weight: bold;
    color: $white;
    @include mobile {
      font-size: 3rem;
      line-height: 3.4rem;
    }
  }

  &__container {
    position: relative;
    z-index: 5;
  }

  &__content {
    background: $white;
    margin-top: -15.5rem;
    padding: 0 0 15.1rem;
    border-radius: 1rem;
    @include mobile {
      margin-top: 0;
      padding-bottom: 6rem;
    }
  }

  &__title {
    font-size: 2.8rem;
    line-height: 4.8rem;
    color: $black;
    box-shadow: 0 20px 80px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    padding: 10rem 14.6rem 10rem 10rem;
    margin-bottom: 11.9rem;
    @include mobile {
      font-size: 1.4rem;
      line-height: 1.8rem;
      padding: 2rem 1.5rem;
      margin-bottom: 2rem;
    }
  }

  &__qoute {
    padding-left: 5rem;
    font-size: 2rem;
    line-height: 3rem;
    color: $black;
    position: relative;
    margin-bottom: 12rem;

    &:before {
      display: block;
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      width: 3px;
      background: #2175AA;
    }
  }

  &__list {
    margin: -1.4rem;
  }

  &__li {
    padding: 1.4rem;
  }

  &__text-col {
    margin: 13rem 0;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    @include mobile {
      margin: 2rem 0;
      display: block;
    }
  }

  &__text-list {
    margin: -1.5rem;
    @include mobile {
      margin: -1rem;
    }
  }

  &__text-li {
    padding: 1.5rem;
    @include mobile {
      padding: 1rem;
    }
  }

  &__text-item {
    font-size: 2rem;
    line-height: 3rem;
    color: $black;
    @include mobile {
      font-size: 1.4rem;
      line-height: 1.8rem;
    }
  }

  &__text {
    font-size: 2rem;
    line-height: 3.4rem;
    color: $black;

    &--right-col {
      flex: 1;
      padding-left: 3.8rem;
      @include mobile {
        flex: none;
        padding-left: 0;
      }
    }
    @include mobile {
      font-size: 1.4rem;
      line-height: 1.8rem;
    }
  }

  &__big-text {
    width: 49.6rem;
    @include smallDesktop {
      width: auto;
    }
  }

  &__big-title {
    font-size: 2rem;
    line-height: 3.4rem;
    color: $black;
    padding-bottom: 1.1rem;
    border-bottom: 1px solid $black;
    @include mobile {
      font-size: 1.4rem;
      line-height: 2rem;
    }
  }

  &__big-number {
    font-size: 12rem;
    line-height: 12rem;
    padding-top: 2rem;
    padding-bottom: 2rem;
    border-bottom: 1px solid $black;
    @include smallDesktop {
      font-size: 8rem;
      line-height: 8rem;
    }
    @include mobile {
      font-size: 3rem;
      line-height: 3.4rem;
      padding: 1rem 0;
      margin-bottom: 2rem;
    }
  }
}
