.form-vacance {
  padding-top: 7.4rem;
  border-top: 1px solid $primaryText;

  @include mobile {
    padding-top: 3rem;
  }

  &__header {
    font-size: 3.6rem;
    line-height: 4.4rem;
    color: $black;
    font-weight: bold;
    margin-bottom: 3rem;

    @include mobile {
      font-size: 2.4rem;
      line-height: 2.8rem;
      margin-bottom: 1.5rem;
    }
  }

  &__desc {
    font-size: 2rem;
    line-height: 3rem;
    color: $primaryText;
    margin-bottom: 1.8rem;

    @include mobile {
      font-size: 1.6rem;
      line-height: 1.9rem;
      margin-bottom: 1rem;
    }
  }

  &__content {
    margin-left: auto;
    width: 60rem;

    @include smallDesktop {
      width: 100%;
      margin-left: 0;
    }
  }

  &__field-col {
    margin-bottom: 3.6rem;

    @include mobile {
      margin-bottom: 2rem;
    }
  }

  &__field-list {
    margin: -2.2rem;

    @include mobile {
      margin: -1rem;
    }
  }

  &__field-li {
    padding: 2.2rem;

    @include mobile {
      padding: 1rem;
    }
  }

  &__file {
    margin-bottom: 4.1rem;

    @include mobile {
      margin-bottom: 2rem;
    }
  }

  &__btn {
    @include fromMobile {
      width: 17.8rem;
      height: 5.2rem;
      font-size: 1.6rem;
      margin-bottom: 1.4rem;
    }

    @include mobile {
      margin-bottom: 1.2rem;
      width: 20rem;
    }
  }

  &__warning {
    font-size: 1.3rem;
    line-height: 1.8rem;
    color: rgba($primaryText, .5);
  }

  .field {
    @include fromMobile {
      font-size: 2rem;
      line-height: 2.3rem;
      height: 3.7rem;
      padding-bottom: 2rem;

      &--textarea {
        height: 3.7rem;
        min-height: 3.7rem;
        padding-bottom: 0;
      }
    }
  }
}
