.aside-menu {
  $self: &;
  position: fixed;
  left: 1rem;
  top: 1rem;
  bottom: 1rem;
  z-index: 1005;
  width: 12.6rem;
  padding: 1rem 1.3rem 0;
  background: $white;
  border-radius: 1rem;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  display: flex;
  flex-direction: column;

  @include less600 {
    overflow: auto;
  }

  @include mobile {
    display: none;
  }

  &--bussiness {
    padding-bottom: 2rem;
  }

  &__btn-col {
    background: rgba(#C5C5C5, .3);
    border-radius: 1.7rem;
    padding: .3rem;
    margin-bottom: 3rem;
  }

  &__btn-list {
    margin: -.3rem;
  }

  &__btn-li {
    padding: .3rem;
  }

  &__btn {
    border-radius: 2rem;
    padding: 0 .6rem;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.2rem;
    color: rgba($black, .5);
    height: 2.8rem;
    transition: color .3s, background .3s;

    #{$self}__btn-li.active & {
      background: $white;
      color: $black;
    }

    @include hover {
      color: $green!important;
      background: $white;
    }
  }

  &__service-col {
    margin-bottom: 3rem;
  }

  &__service-list {
    margin: -.7rem;

    @include less850 {
      margin: -1.4rem;
    }

    @include less650 {
      margin: -1rem;
    }
  }

  &__service-li {
    padding: .7rem;

    @include less850 {
      padding: 1.4rem;
    }

    @include less650 {
      padding: 1rem;
    }
  }

  &__service-item {
    text-align: center;
    display: block;
  }

  &__service-icon {
    font-size: 3rem;
    color: $primaryText;
    margin-bottom: .5rem;

    @include less850 {
      display: none!important;
    }
  }

  &__service-desc {
    font-size: 1.3rem;
    line-height: 1.9rem;
    text-align: center;
    color: rgba($primaryText, .5);
    transition: color .3s;

    #{$self}__service-item:hover & {
      @include fromDesktop {
        color: $green;
      }
    }
  }

  &__pay-col {
    padding: 1rem 0;
    background: $lightBlue;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: -1.3rem;
    width: calc(100% + 2.6rem);
    margin-top: 3rem;
    text-align: center;
    transition: background .3s;
    margin-top: auto;

    @include hover {
      background: #50A9DF;
    }
  }

  &__pay {

  }

  &__pay-icon {
    font-size: 3rem;
    margin-bottom: .6rem;
  }

  &__pay-desc {
    font-size: 1.3rem;
    line-height: 1.6rem;
    color: $white;
  }

  &__basket-col {
    background: $green;
    border-radius: 0 0 1rem 1rem;
    margin-left: -1.3rem;
    width: calc(100% + 2.6rem);
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background .3s;
    position: relative;

    @include hover {
      background: #79BA54;
    }
  }

  &__basket {
    position: relative;
    pointer-events: none;
  }

  &__basket-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 1rem 0;
  }

  &__basket-icon {
    font-size: 5rem;
    color: $white;
    margin-bottom: .5rem;
  }

  &__counter {
    height: 2rem;
    min-width: 2rem;
    padding: 0 .2rem;
    background: $white;
    border-radius: 50%;
    position: absolute;
    right: -.2rem;
    top: -.2rem;
    font-size: 1.2rem;
    line-height: 1.2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color: $black;
  }

  &__basket-form {
    position: absolute;
    left: 100%;
    bottom: 1rem;
    z-index: 999;
  }
}
