.card-partner {
  $self: &;
  display: block;
  width: 100%;
  height: 100%;
  background: $primaryBackground;
  border-radius: 1rem;
  padding: 5.7rem 3rem 7rem;
  text-align: center;
  box-shadow: 0px 0 0 rgba(32, 76, 89, 0.2);
  transition: background .3s, box-shadow .3s;

  @include mobile {
    padding: 2rem .5rem;
  }

  @include hover {
    background: $white;
    box-shadow: 0px 10px 52px rgba(32, 76, 89, 0.2);
  }

  &__img-col {
    height: 12rem;
    background: $white;
    border-radius: 3rem;
    border: 1px solid #EFF3F9;
    margin-bottom: 3rem;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 0 0 rgba(32, 76, 89, 0.1);
    transition: box-shadow .3s;

    @include mobile {
      width: 12rem;
      height: 12rem;
      margin-bottom: 2rem;
    }

    #{$self}:hover & {
      @include fromDesktop {
        box-shadow: 0px 16px 52px rgba(32, 76, 89, 0.1);
      }
    }
  }

  &__img {
    max-width: calc(100% - 2rem);
    max-height: calc(100% - 2rem);
  }

  &__header {
    font-size: 2rem;
    line-height: 3rem;
    color: $primaryText;
    text-align: center;

    @include mobile {
      font-size: 1.6rem;
      line-height: 1.8rem;
    }
  }
}
