.tariff-slider {
  padding-bottom: 2.1rem;
  opacity: 0;
  height: 9.7rem;
  max-height: 9.7rem;

  @include hover {
    .arrow {
      opacity: 1;
    }
  };

  &.slick-initialized {
    opacity: 1;
    height: auto;
    max-height: none;
  }

  .slick-track {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
  }

  &__slide-col {
    height: auto;
    float: none;
    padding-left: 1.5rem;
  }

  &__slide {
    height: 100%;
    background: rgba(255, 255, 255, 0.8);
    backdrop-filter: blur(40px);
    border-radius: 10px;
    padding: 1rem 1.3rem 1.5rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-width: 20rem;

    &--equipment {
      background: rgba(215, 221, 225, 0.8);
    }
  }

  &__header {
    font-size: 1.3rem;
    line-height: 1.9rem;
    color: $primaryText;
    font-weight: 500;
    margin-bottom: 1.1rem;
    width: 15.2rem;

    @include mobile {
      font-size: 1rem;
      line-height: 1.6rem;
    }
  }

  &__bottom {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  &__price {
    font-size: 1.4rem;
    line-height: 1.7rem;
    color: $primaryText;
    font-weight: bold;
    flex: 1;
    padding-right: 2rem;
  }

  .slick-dots {
    button {
      width: .8rem;
      height: .8rem;
      background: rgba($white, .6);
    }
  }

  .arrow {
    opacity: 0;
    color: $white;
    font-size: 4rem;
    transform: translateY(-74%);
    transition: opacity .3s, color .3s;

    &--prev {
      left: -3.4rem;

      @include lessFullHd {
        left: .5rem;
      };
    }

    &--next {
      right: 2.6rem;
    }
  }
}
