.form-pay {
  background: $white;
  box-shadow: 0px 20px 80px rgba(32, 76, 89, 0.1);
  padding: 6.3rem 0 7rem 10.3rem;
  position: relative;

  @include mobile {
    padding: 4rem 2rem;
  }

  &__picture {
    position: absolute;
    right: 0;
    bottom: 0;

    @include mobile {
      display: none;
    }
  }

  &__content {
    width: 54.7rem;
    position: relative;
    z-index: 5;

    @include mobile {
      width: 100%;
    }
  }

  &__list-col {
    margin-bottom: 5rem;

    @include mobile {
      margin-bottom: 2.5rem;
    }
  }

  &__list {
    margin: -1.5rem;

    @include mobile {
      margin: -.7rem;
    }
  }

  &__li {
    padding: 1.5rem;

    @include mobile {
      padding: .7rem;
    }
  }
}
