.card-tariff {
  display: flex;
  flex-wrap: wrap;
  color: $black;
  align-items: center;

  &__header {
    font-size: 1.3rem;
    line-height: 2rem;
    flex: 1;
    padding-right: 2rem;

    @include smallDesktop {
      font-size: 1.2rem;
      line-height: 1.5rem;
      padding-right: 1rem;
    }
  }

  &__price {
    font-size: 1.3rem;
    line-height: 2rem;
    text-align: right;
    font-weight: 500;
    width: 9.2rem;

    @include smallDesktop {
      font-size: 1.1rem;
      line-height: 1.3rem;
      width: 7.8rem;
    }
  }

  &__btn-close {
    margin-left: 1.7rem;
    font-size: 2rem;
    line-height: 0;
    color: $primaryBackground;
    transition: color .3s;

    @include smallDesktop {
      font-size: 1.4rem;
      margin-left: .8rem;
    }

    @include hover {
      color: $green;
    }
  }
}
