.search {
  $self: &;
  background: $white;
  padding-bottom: 17rem;

  @include mobile {
    padding-bottom: 6rem;
  }

  &__header {
    font-size: 3.6rem;
    line-height: 4.4rem;
    font-weight: bold;
    color: $black;
    margin-bottom: 6.7rem;

    @include mobile {
      font-size: 2.4rem;
      line-height: 2.8rem;
      margin-bottom: 4.5rem;
    }
  }

  &__list-col {
    @include mobile {
      margin-top: 4rem;
    }
  }

  &__list {
    margin: -1.5rem;
  }

  &__li {
    padding: 1.5rem;
    position: relative;

    &:after {
      display: block;
      content: '';
      position: absolute;
      left: 1.5rem;
      right: 1.5rem;
      top: 100%;
      height: 1px;
      background: $primaryText;
    }

    &:first-child {

      &:before {
        display: block;
        content: '';
        position: absolute;
        left: 1.5rem;
        right: 1.5rem;
        bottom: 100%;
        height: 1px;
        background: $primaryText;
      }
    }
  }

  &__item {

  }

  &__title {
    font-size: 1.8rem;
    line-height: 2.2rem;
    color: #2175AA;
    margin-bottom: .8rem;
    display: block;
    transition: color .3s;

    @include hover {
      color: $green;
    }
  }

  &__desc {
    font-size: 1.4rem;
    line-height: 2.4rem;
    color: #3A5565;
  }
}
