@mixin hover {
  @include fromDesktop {
    &:hover {
      @content;
    }
  }
}

// fonts
@mixin inter {
  font-family: "Inter";
}

@mixin sourceSans {
  font-family: "Source Sans Pro";
}

@mixin roboto {
  font-family: "Roboto";
}

// brakepoints
@mixin fromDesktop {
  @media screen and (min-width: 1200px) {
    @content;
  }
}

@mixin fromMobile {
  @media screen and (min-width: 981px) {
    @content;
  }
}

@mixin k4 {
  @media screen and (max-width: 2559px) {
    @content;
  }
}

@mixin fullHd {
  @media screen and (max-width: 1919px) {
    @content;
  }
}

@mixin m1640 {
  @media screen and (max-width: 1640px) {
    @content;
  }
}

@mixin lessFullHd {
  @media screen and (max-width: 1550px) {
    @content;
  }
}

@mixin asideDesktop {
  @media screen and (max-width: 1400px) {
    @content;
  }
}

@mixin desktop {
  @media screen and (max-width: 1440px) {
    @content;
  }
}

@mixin fromNormalDesktop {
  @media screen and (min-width: 1440px) {
    @content;
  }
}

@mixin smallDesktop {
  @media screen and (max-width: 1279px) {
    @content;
  }
}

@mixin mobile {
  @media only screen and (max-width: 980px) {
    @content;
  }
}

@mixin lessHorizontalTablet {
  @media screen and (max-width: 1023px) {
    @content;
  }
}

@mixin lessVerticalTablet {
  @media screen and (max-width: 767px) {
    @content;
  }
}

@mixin smallMobile {
  @media screen and (max-width: 500px) {
    @content;
  }
}

// height brakepoints
@mixin less850 {
  @media only screen and (max-height: 849px) {
    @content;
  }
}

@mixin less650 {
  @media only screen and (max-height: 650px) {
    @content;
  }
}

@mixin less600 {
  @media only screen and (max-height: 599px) {
    @content;
  } 
}

// other
@mixin hide {
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
  pointer-events: none;
}

@mixin show {
  position: static;
  opacity: 1;
  pointer-events: auto;
}

@mixin btn {
  display: flex;
  justify-content: center;
  align-items: center;
  background: $aqua;
  border-radius: 4px;
  color: #fff;
  font-size: 14px;
  line-height: 17px;
  height: 48px;
  padding: 0 10px;
  text-transform: uppercase;
  transition: background 0.3s;

  @include hover {
    background: $aquaHover;
  }

  &:active {
    box-shadow: inset 0 0 10px 1px rgba($black, 0.3);
  }
}

@mixin btnActive {
  &:active {
    box-shadow: inset 0 0 5px 1px rgba($black, 0.3);
  }
}

@mixin imgScale {
  transition: transform 0.3s;

  @include hover {
    transform: scale3d(1.1, 1.1, 1.1);
  }
}

@mixin safariOverflowFix {
  -webkit-mask-image: -webkit-radial-gradient(white, black);
}
