.services {
  &__header {
    font-size: 3.6rem;
    line-height: 4.4rem;
    color: $black;
    font-weight: bold;
    margin-bottom: 3rem;

    @include mobile {
      font-size: 2.4rem;
      line-height: 2.8rem;
      margin-bottom: 1.5rem;
    }
  }

  &__desc {
    font-size: 2rem;
    line-height: 3rem;
    color: $primaryText;
    margin-bottom: 10rem;

    @include mobile {
      font-size: 1.4rem;
      line-height: 1.8rem;
      margin-bottom: 5rem;
    }
  }
}
