.btn {
  $self: &;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
  height: 4.4rem;
  font-size: 1.4rem;
  line-height: 2rem;
  font-weight: bold;
  border-radius: .8rem;
  padding: 0 2rem;
  transition: background .3s, color .3s, border-color .3s;

  @include mobile {
    font-size: 1.2rem;
    line-height: 1.5rem;
  }

  &:active {
    box-shadow: inset 0 0 5px 1px rgba($black, .3);
  }

  &--toggle {
    justify-content: space-between;
    cursor: pointer;

    .toggle {
      pointer-events: none;
    }
  }

  &--green {
    background: $green;
    color: $white;

    @include hover {
      background: $lightBlue;
    }
  }

  &--black {
    background: $black;
    color: $white;

    @include hover {
      background: $green;
    }
  }

  &--light-blue {
    background: $blueDarkBg;
    color: $primaryText;

    @include hover {
      background: $green;
      color: $white;
    }
  }

  &--white {
    background: $white;
    color: $primaryText;

    @include hover {
      background: $green;
      color: $white;
    }
  }

  &--transparent {
    background: none;
    color: $white;
    padding: 0;
    height: auto;

    @include hover {
      color: $green;
    }
  }

  &--light-green {
    background: rgba($green, .8);
    color: $white;

    @include hover {
      background: $lightBlue;
      color: $white;
    }
  }

  &--dark-blue {
    background: $primaryText;
    color: $white;

    @include hover {
      background: $green;
      color: $white;
    }
  }

  &--border-transparent {
    background: none;
    border: 1px solid #D9DCE1;

    @include hover {
      background: $green;
      border-color: $green;
      color: $white;
    }
  }

  &--blue {
    background: #2175AA;
    color: $white;

    @include hover {
      background: $green;
      color: $white;
    }
  }

  &--light-blue-blue {
    background: $primaryText;
    color: $white;

    @include hover {
      background: $lightBlue;
      color: $white;
    }
  }

  &__text {
    #{$self}--toggle & {
      flex: 1;
      padding-right: 2rem;
    }
  }
}
