.tariff-form {
  color: $black;

  &__main-header {
    font-size: 2.2rem;
    line-height: 2.7rem;
    font-weight: bold;
    padding-bottom: 2rem;
    border-bottom: 1px solid rgba($primaryText, .3);
    margin-bottom: 1.4rem;

    @include smallDesktop {
      font-size: 1.6rem;
      line-height: 1.8rem;
      padding-bottom: 1rem;
    }
  }

  &__content-col {
    margin-bottom: 1.5rem;
    max-height: 40rem;
    overflow: auto;
    overflow-x: hidden;
    padding-right: 2rem;
    margin-right: -2rem;

    @include smallDesktop {
      max-height: 30rem;
      margin-bottom: 1rem;
    }
  }

  &__overflow-container {
    overflow: hidden;
    border-bottom: 1px solid rgba($primaryText, .3);
    padding-bottom: 1rem;

    @include smallDesktop {
      padding-bottom: .5rem;
    }
  }

  &__list {
    margin: -1.4rem;
  }

  &__li {
    padding: 1.4rem;
    position: relative;

    &:after {
      display: block;
      content: '';
      position: absolute;
      left: .8rem;
      right: .8rem;
      top: 100%;
      height: 1px;
      background: $primaryText;
    }

    &:last-child:after {
      display: none;
    }
  }

  &__item {

  }

  &__header-col {
    border-bottom: 1px solid rgba($primaryText, .3);
    padding-bottom: 1rem;
    margin-bottom: 1rem;

    @include smallDesktop {
      margin-bottom: .5rem;
      padding-bottom: .5rem;
    }
  }

  &__header {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 1.7rem;

    @include smallDesktop {
      margin-bottom: .8rem;
    }
  }

  &__header-text {
    flex: 1;
    padding-right: 2rem;
    font-size: 1.8rem;
    line-height: 2.2rem;
    font-weight: bold;

    @include smallDesktop {
      font-size: 1.4rem;
      line-height: 1.6rem;
      padding-right: 1rem;
    }
  }

  &__price {
    font-size: 1.8rem;
    line-height: 2.2rem;
    font-weight: bold;

    @include smallDesktop {
      font-size: 1.4rem;
      line-height: 1.8rem;
    }
  }

  &__btn-close {
    font-size: 2rem;
    line-height: 0;
    color: $primaryBackground;
    margin-left: 1.7rem;
    transition: color .3s;
    display: block;

    @include smallDesktop {
      font-size: 1.4rem;
      margin-left: .8rem;
    }

    @include hover {
      color: $green;
    }
  }

  &__desc {
    font-size: 1.2rem;
    line-height: 1.8rem;
    color: $primaryText;

    @include smallDesktop {
      font-size: 1rem;
      line-height: 1.2rem;
    }
  }

  &__grey-text {
    color: #979797;
  }

  &__link-text {
    color: #2175AA;
  }

  &__sub-list-col {
  }

  &__sub-list {
    margin: -1rem;

    @include smallDesktop {
      margin: -.5rem;
    }
  }

  &__sub-li {
    position: relative;
    padding: 1rem;

    @include smallDesktop {
      padding: .5rem;
    }

    &:after {
      display: block;
      content: '';
      position: absolute;
      left: 1rem;
      right: 1rem;
      top: 100%;
      height: 1px;
      background: rgba($primaryText, .3);

      @include smallDesktop {
        left: .5rem;
        right: .5rem;
      }
    }

    &:last-child:after {
      display: none;
    }
  }

  &__bottom-col {

  }

  &__bottom-header {
    font-size: 1.8rem;
    line-height: 2.2rem;
    font-weight: bold;
    margin-bottom: .5rem;

    @include smallDesktop {
      font-size: 1.5rem;
      line-height: 1.7rem;
    }
  }

  &__bottom-desc {
    font-size: 1.2rem;
    line-height: 1.8rem;
    color: $primaryText;

    @include smallDesktop {
      font-size: 1rem;
      line-height: 1.6rem;
    }
  }
}
