.card-big-sale {
  padding: 1.5rem;
  background: $primaryBackground;
  display: flex;
  flex-wrap: wrap;
  color: $primaryText;
  border-radius: 1.5rem;
  box-shadow: 0px 0px 0px rgba(32, 76, 89, 0.17);
  transition: box-shadow .3s, transform .3s;

  @include mobile {
    display: block;
  }

  @include hover {
    transform: translate3d(0, -1rem, 0);
    box-shadow: 0px 30px 60px rgba(32, 76, 89, 0.17);
  }

  &__link {
    display: block;
    overflow: hidden;
    border-radius: 1rem;
    width: 68.5rem;
    height: 38.4rem;

    @include smallDesktop {
      width: 40rem;
      height: 22.4rem;
    }

    @include mobile {
      width: 31.5rem;
      height: 17.6rem;
      background-size: 100% 100%;
      margin-bottom: 3rem;
    }
  }

  &__img-col {
    width: 100%;
    height: 100%;
    border-radius: 1rem;

    img {
      border-radius: 1rem;
    }
  }

  &__content-col {
    flex: 1;
    padding-left: 5rem;
    padding-top: 1.5rem;
    padding-right: 2rem;

    @include mobile {
      padding-left: 0;
      padding-right: 0;
      padding-top: 0;
    }
  }

  &__title {
    font-size: 1.4rem;
    line-height: 1.7rem;
    margin-bottom: 1.9rem;

    @include mobile {
      margin-bottom: 1.2rem;
    }
  }

  &__header {
    font-size: 3rem;
    line-height: 3.6rem;
    margin-bottom: 3rem;

    @include mobile {
      font-size: 2.4rem;
      line-height: 2.8rem;
      margin-bottom: 1.5rem;
    }
  }

  &__desc {
    font-size: 1.4rem;
    line-height: 2.4rem;
    margin-bottom: 4rem;

    @include mobile {
      margin-bottom: 2rem;
    }
  }

  &__btn {
    @include fromMobile {
      width: 18rem;
      height: 5.2rem;
      font-size: 1.6rem;
    }

    @include mobile {
      width: 18rem;
    }
  }
}
