.advantages {
  $self: &;
  margin: 13rem 0 10rem;
  display: flex;
  flex-wrap: wrap;
  padding: 8rem 4rem 10.2rem 0;
  background: no-repeat center;
  background-size: 100% auto;

  @include smallDesktop {
    background-size: 100% 100%;
  }

  @include mobile {
    background: none!important;
    margin: 3rem 0;
    padding: 0;
  }

  &__header {
    font-size: 2rem;
    line-height: 3rem;
    font-weight: bold;
    color: $black;
    margin-bottom: 3.8rem;

    @include mobile {
      margin-bottom: 1.8rem;
    }
  }

  &__content-col {
    margin-left: auto;
    width: 46.3rem;

    @include mobile {
      width: 100%;
      margin-left: 0;
    }
  }

  &__list {
    margin: -2.5rem;

    @include mobile {
      margin: -1rem;
    }
  }

  &__li {
    padding: 2.5rem;

    @include mobile {
      padding: 1rem;
    }
  }

  &__item {
    color: $black;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
  }

  &__icon {
    font-size: 6rem;
    color: $green;
    transition: color .3s;

    #{$self}__item:hover & {
      @include fromDesktop {
        color: $lightBlue;
      }
    }
  }

  &__content {
    flex: 1;
    padding-left: 3rem;
    padding-top: .4rem;
  }

  &__title {
    font-size: 1.3rem;
    line-height: 2.2rem;
    font-weight: bold;
    margin-bottom: 1.5rem;

    @include mobile {
      margin-bottom: .4rem;
    }
  }

  &__desc {
    font-size: 1.2rem;
    line-height: 2rem;
    color: $primaryText
  }
}
