.section-header {
  padding-bottom: 1.5rem;
  border-bottom: 1px solid $primaryText;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 2.6rem;

  @include mobile {
    margin-bottom: 1.5rem;
  }

  &__text {
    font-size: 3rem;
    line-height: 3.6rem;
    font-weight: bold;
    color: $black;
    flex: 1;
    padding-right: 2rem;

    @include mobile {
      font-size: 1.8rem;
      line-height: 2.2rem;
    }
  }

  &__btn {
    width: auto;
    border-radius: 30px;
  }

  &__address {
    font-size: 3rem;
    line-height: 3.6rem;
    font-weight: 300;
    font-style: italic;
    margin-left: 0.3rem;
    color: rgba($black, 0.6);

    @include mobile {
      font-size: 1.6rem;
      line-height: 2rem;
    }
  }

  &__select-col {
    margin-left: auto;

    @include lessVerticalTablet {
      width: 100%;
      margin-left: 0;
      margin-top: 2rem;
    }
  }

  &__select-list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: -0.6rem;
  }

  &__select-li {
    padding: 0.6rem;
  }

  &__select-year {
    .select {
      &__btn {
        min-width: 14.4rem;
      }
    }
  }

  .select {
    &__head {
      background: #E7ECF5;
    }

    &__text {
      color: #204C59;
    }
  }
}
