.btn-oferta {
  display: inline-flex;
  background: #000;
  border-radius: 1rem;
  align-items: center;
  padding: 0 4rem 0 1.2rem;
  height: 6.9rem;
  transition: background .3s;

  @include hover {
    background: $green;
  };

  &__icon {
    font-size: 4.4rem;
    color: $white;
    margin-right: 2.5rem;
  }

  &__desc {
    font-size: 2rem;
    line-height: 2.4rem;
    color: $white;
    font-weight: bold;
  }
}
