.card-channel {
  $self: &;
  background: $primaryBackground;
  padding: 3.7rem 1.1rem 4rem;
  border-radius: 10px;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  text-align: center;
  box-shadow: 0px 0px 0px rgba(32, 76, 89, 0.2);
  cursor: pointer;
  transition: background .3s, box-shadow .3s;

  @include mobile {
    padding: 2rem .5rem;
  }

  @include hover {
    background: $white;
    box-shadow: 0px 10px 52px rgba(32, 76, 89, 0.2);
  }

  &__img-col {
    width: 12rem;
    height: 12rem;
    border-radius: 3rem;
    background: $white;
    margin: 0 auto;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 2rem;
    padding: 1rem;
    box-shadow: 0px 0 0 rgba(32, 76, 89, 0.1);
    transition: box-shadow .3s;

    #{$self}:hover & {
      @include fromDesktop {
        box-shadow: 0px 16px 52px rgba(32, 76, 89, 0.1);
      }
    }
  }

  &__img {

  }

  &__title {
    font-size: 2rem;
    line-height: 3rem;
    color: $primaryText;
    margin-bottom: 2.7rem;

    @include mobile {
      font-size: 1.6rem;
      line-height: 1.8rem;
    }
  }

  &__number {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background: #E7ECF5;
    border-radius: 18.5px;
    padding: .7rem 1.6rem;
    margin-top: auto;
    font-size: 1.3rem;
  }
}
