.card-pay {
  &__header {
    font-size: 3.6rem;
    line-height: 4.4rem;
    font-weight: bold;
    color: $black;
    margin-bottom: 3rem;

    @include mobile {
      font-size: 2.2rem;
      line-height: 2.6rem;
      margin-bottom: 1.5rem;
    }
  }

  &__link-col {
    max-width: 50rem;
    margin-bottom: 7.4rem;

    @include mobile {
      margin-bottom: 3rem;
    }
  }

  &__link-list {

  }

  &__link-li {

  }

  &__link {
    font-size: 2rem;
    line-height: 3rem;
    color: $darkBlue;
    transition: color .3s;

    @include mobile {
      font-size: 1.6rem;
      line-height: 2rem;
    }

    @include hover {
      color: $green;
    }
  }

  &__form-pay {
    margin-bottom: 22rem;

    @include mobile {
      margin-bottom: 5rem;
    }
  }

  &__tab-li {
    display: none;

    &.active {
      display: block;
    }
  }
}
