.faq-list {
  $self: &;

  &__list {
    margin: -.6rem;
  }

  &__li {
    padding: .6rem;
  }

  &__item {
    background: $primaryBackground;
    border: 1px solid rgba(32, 76, 89, 0.05);
    border-radius: 6px;
    padding: 1rem 1.9rem 1rem 1.6rem;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    transition: background .3s;

    @include hover {
      background: $green;
    }
  }

  &__text {
    font-size: 2.3rem;
    color: $primaryText;
    font-weight: 600;
    flex: 1;
    padding-right: 2rem;
    transition: color .3s;

    #{$self}__item:hover & {
      @include fromDesktop {
        color: $white;
      }
    }

    @include mobile {
      font-size: 1.4rem;
    }
  }

  &__icon {
    color: #C5D1D9;
    font-size: 1.8rem;
    transition: color .3s;

    #{$self}__item:hover & {
      @include fromDesktop {
        color: $white;
      }
    }
  }
}
