.field {
  font-size: 1.3rem;
  line-height: 1.8rem;
  color: $primaryText;
  padding-bottom: 1rem;
  border-bottom: 1px solid rgba($primaryText, .3);
  background: none;
  height: 2.4rem;
  line-height: 2.4rem;
  width: 100%;

  @include mobile {
    font-size: 1.2rem;
    line-height: 1.8rem;
  }

  &::placeholder {
    color: rgba($primaryText, .5);
  }

  &--textarea {
    height: 5.8rem;
    min-height: 5.8rem;
    line-height: 1.8rem;
    resize: vertical;
    border-width: 0 0 1px;
  }

  &--white-50 {
    color: rgba($white, .5);
    border-color: rgba($white, .5);

    &::placeholder {
      color: rgba($white, .5);
    }
  }

  &--white {
    color: $white;
    border-color: rgba($white, .5);

    &::placeholder {
      color: $white;
    }
  }
}
