.swiper {
  &-pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    bottom: 0rem;
    left: 0;
    right: 0;
  }

  &-pagination-bullet  {
    margin-left: 1.5rem!important;
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    background: rgba(#fff, .2);
    font-size: 0;
    line-height: 0;
    opacity: 1;
    transition: background .3s;

    @include hover {
      background: $white;
    }

    &.swiper-pagination-bullet-active {
      background: #fff;
    }

    &:first-child {
      margin-left: 0!important;
    }
  }
}
