.gallery {
  margin-bottom: 18rem;

  @include mobile {
    margin-bottom: 10rem;
  }

  &__section-header {
    margin-bottom: 4rem;

    @include mobile {
      margin-bottom: 2rem;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    margin: -1.5rem;
  }

  &__li {
    padding: 1.5rem;
    width: calc(100% / 2);

    @include mobile {
      width: 100%;
    }
  }

  &__item {
    padding: 1rem 3rem 4.6rem;
    background: $primaryBackground;
    border-radius: 1.5rem;

    @include mobile {
      padding: 1rem 1.5rem 2rem;
    }
  }

  &__img-col {
    display: block;
    margin-left: -2rem;
    width: calc(100% - -4rem);
    overflow: hidden;
    margin-bottom: 3.2rem;
    border-radius: 1rem;

    @include mobile {
      margin-bottom: 2rem;
      margin-left: 0;
      width: 100%;
    }
  }

  &__img {
    height: 25.6rem;

    @include mobile {
      height: 20rem;
    }
  }

  &__header {
    display: block;
    font-size: 1.6rem;
    line-height: 2.4rem;
    color: $primaryText;
    font-weight: bold;
    transition: color .3s;
    margin-bottom: 2.4rem;

    @include mobile {
      margin-bottom: 1.2rem;
    }

    @include hover {
      color: $green;
    }
  }

  &__btn {
    width: 14.8rem;
    height: 4rem;
  }
}
