.faq {
  margin: 4.5rem 0 0 0;

  &__list {
    padding-bottom: 2.9rem;

    @include mobile {
      padding-bottom: 1.6rem;
    }
  }

  &__list-item {
    padding-bottom: 2.1rem;

    @include mobile {
      padding-bottom: 1.5rem;
    }
  }

  .faq__list-item.active {
    .faq-card {
      background-color: $white;
      box-shadow: 0px 10px 52px rgba(32, 76, 89, 0.2);
    }

    .faq-card__open {
      display: block;
      width: 100%;
    }

    .js-accordion-btn {
      background: $primaryText;
      color: $white;
    }

    .faq-card__button-text--open {
      display: none;
    }

    .faq-card__button-text--close {
      display: block;
    }
  }
}
