.articles {
  margin-bottom: 18rem;

  @include mobile {
    margin-bottom: 9rem;
  }

  &__list-col {
    margin-bottom: 6rem;

    @include mobile {
      margin-bottom: 3rem;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    margin: -2rem;
  }

  &__li {
    padding: 2rem;
    width: 34.14%;

    @include mobile {
      width: 100%;
    }

    &--big {
      width: 65.86%;

      @include mobile {
        width: 100%;
      }
    }
  }

  &__bottom {
    padding-top: 3rem;
    border-top: 1px solid $primaryText;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  &__btn {
    width: 19.6rem;
    height: 3.7rem;
    border-radius: 18.5px;
  }
}
