.news {
  overflow: hidden;
  background: $white;
  margin: $mainPageSectionGap;

  @include mobile {
    margin: 4rem 0;
  }

  &--blue {
    background: $primaryBackground;
    padding: 5rem 0 10.1rem;
    margin: 0;

    @include mobile {
      padding: 4rem 0;
      margin: 0;
    }
  }

  &__section-header {
    margin-bottom: 4.6rem;

    @include mobile {
      margin-bottom: 1.5rem;
    }
  }

  .section-header {
    &__btn {
      width: 13rem;
      height: 3.7rem;
    }
  }

  &__list-col {
    @include mobile {
      overflow: auto;
      padding-bottom: 1rem;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: -4rem -1.3rem;

    @include mobile {
      margin: 0;
      display: block;
      font-size: 0;
      white-space: nowrap;
    }
  }

  &__li {
    padding: 4rem 1.3rem;
    width: calc(100% / 3);

    @include mobile {
      padding: 0;
      width: auto;
      display: inline-block;
      margin-left: 3rem;
      vertical-align: top;

      &:first-child {
        margin-left: 0;
      }
    }
  }

  &__bottom-col {
    padding-top: 3rem;
    border-top: 1px solid $primaryText;
    margin-top: 8.6rem;

    @include mobile {
      margin-top: 3rem;
    }
  }

  &__btn-more {
    margin: 0 auto;
    width: 19.6rem;
    height: 3.7rem;
    border-radius: 2rem;

    @include btnActive;
  }
}
