.page-header {
  position: relative;
  z-index: 999;
  background: $primaryBackground;
  padding: 1.3rem 0;

  @include mobile {
    padding: 0.5rem 0 1rem;
  }

  &__container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    position: relative;
    padding-right: 35rem;
    max-width: 123rem;

    @include lessFullHd {
      max-width: 113.6rem;
      padding-right: 32rem;
    }

    @include desktop {
      &.container {
        @include smallDesktop {
          padding-left: 15.1rem !important;
        }

        @include mobile {
          padding-left: 1.5rem!important;
        }
      }
    }

    @include smallDesktop {
      padding-right: 22rem;
    }

    @include mobile {
      padding-right: 1.5rem;
    }
  }

  &__logo {
    display: block;
    margin-right: 3.7rem;

    @include lessFullHd {
      width: 11rem;
      margin-right: 2.2rem;
    }

    @include desktop {
      margin-right: 1.4rem;
      width: 10rem;
    };

    @include smallDesktop {
      width: 10rem;
    }

    @include mobile {
      margin-right: 0;
      margin-left: 8.8rem;
      width: 6.9rem;
    }
  }

  &__phone-col {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    font-size: 1.3rem;
    line-height: 1.6rem;
    color: $primaryText;

    @include desktop {
      font-size: 1.1rem;
      line-height: 1.3rem;
    }

    @include mobile {
      display: none;
    }
  }

  &__phone-icon {
    margin-right: .7rem;
  }

  &__phone-value {
    font-weight: bold;
    transition: color .3s;

    @include hover {
      color: $green;
    }
  }

  &__link-col {
    margin-left: auto;
    line-height: 0;

    @include mobile {
      display: none;
    }
  }

  &__link-list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    line-height: 0;
  }

  &__link-li {
    margin-left: 1.2rem;
    line-height: 0;

    &:first-child {
      margin-left: 0;
    }

    @include smallDesktop {
      margin-left: .3rem;
    }
  }

  &__link {
    font-size: 1.3rem;
    line-height: normal;
    color: $primaryText;
    transition: color .3s;

    @include hover {
      color: $green;
    }

    @include desktop {
      font-size: 1.2rem;
      line-height: 1.6rem;
    }

    @include smallDesktop {
      font-size: 1.1rem;
      line-height: 1.2rem;
    };
  }

  &__order-form {
    position: absolute;
    right: 1.5rem;
    top: -1.4rem;
    z-index: 50;

    @include mobile {
      display: none;
      position: fixed;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      z-index: 999;

      &.active {
        display: block;
      }
    }
  }

  &__btn-col {
    display: none;
    margin-left: auto;

    @include mobile {
      display: block;
    }
  }

  &__btn-list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: -.7rem;
  }

  &__btn-li {
    padding: .7rem;
    line-height: 0;
  }

  &__btn-icon {
    font-size: 2rem;
    color: #204C59;
  }

  &__btn-form {
    line-height: 0rem;
  }

  &__counter {
    position: absolute;
    right: -.7rem;
    top: 0;
    padding: 0 .3rem;
    height: 1.6rem;
    background: $green;
    border-radius: .7rem;
    font-size: 1rem;
    line-height: 1.5rem;
    color: $white;
    @include sourceSans;
    transform: translateY(-50%);
  }

  &__phone-mob {
    display: block;
  }

  &__btn-form {

  }

  &__basket-mob {
    position: relative;
    line-height: 0;
    display: block;
  }

  &__burger {
    display: none;

    @include mobile {
      display: block;
    }
  }

  &__basket-form {
    display: none;
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 999;

    &.active {
      display: none;

      @include mobile {
        display: block;
      }
    }
  }
}
