.service-join {
  margin-bottom: 13.5rem;

  @include mobile {
    margin-bottom: 4rem;
  };

  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    margin: -1.2rem;

    @include smallDesktop {
      margin: -.6rem;
    };

    @include  mobile {
      display: block;
    };
  }

  &__li {
    padding: 1.2rem;
    width: calc(100% / 3);

    @include smallDesktop {
      padding: .6rem;
    };

    @include mobile {
      width: 100%;
    };
  }

  &__card {
    background: #F4D874;
    box-shadow: 0px 26px 52px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    padding: 4rem 4rem 3.6rem;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;

    @include smallDesktop {
      padding: 2rem;
    };
  }

  &__header {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  &__icon-col {
    width: 8rem;
    height: 8rem;
    border-radius: .8rem;
    background: $white;
    display: flex;
    justify-content: center;
    align-items: center;

    @include smallDesktop {
      width: 6rem;
      height: 6rem;
    };
  }

  &__icon {
    font-size: 4.5rem;
    color: #F4D874;

    @include smallDesktop {
      font-size: 3rem;
    };
  }

  &__header-desc {
    flex: 1;
    padding-left: 3rem;
    font-size: 2rem;
    line-height: 3rem;
    color: $black;
    font-weight: bold;
  }

  &__desc {
    padding-top: 7.4rem;
    flex: 1;
    font-size: 1.8rem;
    line-height: 2.7rem;
    color: $primaryText;

    @include smallDesktop {
      padding-top: 3rem;
      font-size: 1.4rem;
      line-height: 1.9rem;
    };

    @include mobile {
      padding-top: 2rem;
    };
  }
}
