.page-footer {
  overflow: hidden;
  background: $primaryBackground;

  @include mobile {
    padding: 4rem 0 5rem;
  }

  &__head {
    padding: 5.2rem 0 2.8rem;

    @include mobile {
      display: none
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: -4.5rem;
  }

  &__li {
    padding: 4.5rem;
  }

  &__item {

  }

  &__title {
    position: relative;
    z-index: 5;
    display: block;
    font-size: 1.6rem;
    line-height: 1.9rem;
    color: $primaryText;
    font-weight: bold;
    margin-bottom: 2.5rem;
    transition: color .3s;

    @include mobile {
      margin-bottom: 3rem;
    }

    @include hover {
      color: $green;
    }
  }

  &__links-list {
    display: flex;
    flex-wrap: wrap;
    margin: -4.5rem;
  }

  &__links-li {
    padding: 4.5rem;
    max-width: calc(16rem + 9rem);
  }

  &__link-title {
    display: block;
    font-size: 1.2rem;
    line-height: 2rem;
    font-weight: bold;
    color: $primaryText;
    margin-bottom: 1.3rem;
    transition: color .3s;

    @include hover {
      color: $green;
    }
  }

  &__link-list {
    margin: -.2rem;
  }

  &__link-li {
    line-height: 0;
    padding: .2rem;
  }

  &__link {
    font-size: 1.2rem;
    line-height: 2rem;
    color: rgba($primaryText, .8);
    transition: color .3s;

    @include hover {
      color: $green;
    }
  }

  &__bot {
    background: $blueDarkBg;
    padding: 4rem 0 4.6rem;

    @include asideDesktop {
      margin-left: -$asidePadding;
      padding-left: $asidePadding;
    }

    @include mobile {
      padding-top: 0;
      padding-bottom: 0;
      background: none;
    }
  }

  &__bot-container {
    display: flex;
    flex-wrap: wrap;
  }

  &__copy-col {
    width: 37.7rem;
  }

  &__copy {
    font-size: 1.6rem;
    line-height: 1.9rem;
    color: $primaryText;
    margin-bottom: 3rem;
    font-weight: bold;
  }

  &__copy-desc {
    font-size: 1.2rem;
    line-height: 2rem;
    color: $primaryText;
    margin-bottom: 3rem;
  }

  &__address-col {
    margin-left: 8.8rem;
    width: 27rem;

    @include mobile {
      width: 100%;
      margin-left: 0;
      margin-bottom: 4rem;
    }
  }

  &__address-list {
    margin: -.2rem;
    margin-bottom: calc(2.2rem + .8rem);

    @include mobile {
      margin-bottom: 2.2rem;
    }
  }

  &__address-li {
    padding: .2rem;
    font-size: 1.2rem;
    line-height: 2rem;
    color: $primaryText;
  }

  &__address-key {

  }

  &__adress-value {

  }

  &__social-list {
    display: flex;
    flex-wrap: wrap;
    margin: -.6rem;
  }

  &__social-li {
    padding: .6rem;
  }

  &__social-link {
    color: rgba(#849CA7, .8);
    transition: color .3s;
    font-size: 3.4rem;

    @include hover {
      color: $green;
    }
  }

  &__by-col {
    margin-left: auto;

    @include mobile {
      margin-left: 0;
    }
  }

  &__nkh-link {
  }
}
