.popup-gallery {
  $self: &;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(180deg, rgba(32, 76, 89, 0.85) 29.98%, rgba(32, 76, 89, 0) 100%);
  overflow: auto;
  z-index: 1500;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  pointer-events: none;
  transition: opacity .3s;

  &.active {
    opacity: 1;
    pointer-events: auto;
  }

  &__close {
    display: block;
    font-size: 4rem;
    color: $white;
    position: absolute;
    left: calc(100% + 2.5rem);
    top: 0;
    transition: color .3s;

    @include mobile {
      position: static;
      font-size: 2rem;
      color: $green;
      margin-bottom: 2rem;
      margin-left: auto;
    }

    @include hover {
      color: $green;
    }
  }

  &__content-col {
    position: relative;

    @include mobile {
      background: $white;
      height: 100%;
      width: 100%;
      padding: 1.5rem 1.5rem 0;
      overflow: auto;
      overflow-x: hidden;
    }

    &:after {
      @include mobile {
        display: block;
        content: '';
        width: 100%;
        height: 2rem;
      }
    }
  }

  &__content {
    width: 106rem;
    padding: 3rem 3rem 0;
    border-radius: 2rem;
    background: $white;
    position: relative;
    overflow: auto;
    overflow-x: hidden;
    max-height: 97vh;
    height: 77.1rem;

    @include smallDesktop {
      width: 80vw;
      height: 90vh;
    }

    @include mobile {
      width: 100%;
      height: auto;
      max-height: 100vh;
      padding: 0;
      border-radius: 0;
      overflow: hidden;
    }

    &:after {
      display: block;
      content: '';
      width: 100%;
      height: 3.6rem;

      @include mobile {
        height: 2rem;
        display: none;
      }
    }

    #{$self}--img-focus & {
      display: flex;
      flex-direction: column;
    }
  }

  &__img-col {
    margin-bottom: 3.4rem;

    @include mobile {
      margin-bottom: 2rem;
      max-width: 50rem;
    }
  }

  &__img {
    height: 59.1rem;
    background: no-repeat center;
    background-size: cover;
    border-radius: 1rem;

    @include smallDesktop {
      height: 40rem;
    }

    @include mobile {
      height: 30rem;
    }

    #{$self}--img-focus & {
      height: auto;
      text-align: center;
      pointer-events: none;

      .picture {

        &__img {
          object-fit: none;
          width: auto;
          height: auto;
        }
      }
    }
  }

  &__bottom {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    #{$self}--img-focus & {
      display: block;
    }
  }

  &__header {
    font-size: 2rem;
    line-height: 3rem;
    color: $black;
    width: 45.7rem;

    @include smallDesktop {
      width: auto;
      flex: 1;
      padding-right: 3rem;
    }

    @include mobile {
      font-size: 1.4rem;
      line-height: 1.8rem;
      padding-right: 2rem;
    }
  }

  &__social-col {
    #{$self}--img-focus & {
      display: none;
    }
  }

  &__social-title {
    font-size: 2rem;
    line-height: 2.4rem;
    font-weight: bold;
    color: $black;
    margin-bottom: 1rem;

    @include mobile {
      font-size: 1.6rem;
      line-height: 1.9rem;
    }
  }

  &__social-list {
    display: flex;
    flex-wrap: wrap;
    margin: -1rem;

    @include mobile {
      margin: -.5rem;
    }
  }

  &__social-li {
    padding: 1rem;

    @include mobile {
      padding: .5rem;
    }
  }

  &__social-item {
    display: block;
    line-height: 0;
  }

  &__social-icon {
    font-size: 4rem;
    color: #262626;
    transition: color .3s;

    @include mobile {
      font-size: 2rem;
    }

    @include hover {
      color: $green;
    }
  }

  &__arrow {
    position: absolute;
    top: 31rem;
    font-size: 5.2rem;
    color: rgba($white, .3);
    z-index: 50;
    line-height: 0;
    transition: color .3s;

    @include hover {
      color: $green;
    };

    &:active {
      color: $green;
    }

    @include smallDesktop {
      top: 21rem;
    };

    @include mobile {
      top: 18rem;
      color: rgba($green, .4)
    };

    &--prev {
      left: -1.9rem;
      transform: translate(-100%, 0%);

      @include mobile {
        left: 2rem;
        transform: translate(0, 0%);
      };
    }

    &--next {
      right: -1.9rem;
      transform: translate(100%, 0%);

      @include mobile {
        right: 2rem;
        transform: translate(0, 0%);
      };
    }
  }
}
