.big-news {
  color: $primaryText;
  background: $white;
  padding: 8.7rem 0 5rem;

  @include mobile {
    padding: 4rem 0;
  }

  @include asideDesktop {
    .big-news__container {
      padding: 0 7rem!important;

      @include mobile {
        padding: 0!important;
      }
    }
  }

  &__container {
    display: flex;
    flex-wrap: wrap;
    padding: 0 7rem;
  }

  &__content-col {
    width: 39.5rem;
    padding-top: 1rem;

    @include lessVerticalTablet {
      order: 2;
      width: 100%;
    }
  }

  &__date {
    font-size: 1.4rem;
    line-height: 1.7rem;
    margin-bottom: 1.9rem;

    @include lessVerticalTablet {
      font-size: $mobileText;
    }

    @include mobile {
      margin-bottom: 1rem;
    }
  }

  &__header {
    font-size: 3rem;
    line-height: 3.6rem;
    font-weight: bold;
    margin-bottom: 3.8rem;
    display: block;
    transition: color .3s;

    @include hover {
      color: $green;
    }

    @include mobile {
      font-size: 1.8rem;
      line-height: 2rem;
      margin-bottom: 1.8rem;
    }
  }

  &__desc {
    font-size: 1.4rem;
    line-height: 2.4rem;
    margin-bottom: 5.2rem;

    @include lessVerticalTablet {
      font-size: $mobileText;
      margin-bottom: 1.8rem;
    }
  }

  &__btn {
    width: 18rem;
    height: 5.2rem;

    @include mobile {
      height: 4.4rem;
    }
  }

  &__img-col {
    flex: 1;
    margin-left: 7.7rem;

    @include lessVerticalTablet {
      order: 1;
      margin-left: 0;
      margin-bottom: 1.8rem;
      flex: none;
      width: 100%;
    }
  }

  &__picture {
    border-radius: 1.5rem;

    img {
      border-radius: 1.5rem;
    }
  }
}
