.card-news {
  color: $black;

  @include mobile {
    width: 28rem;
    white-space: normal;
  }

  &__img-container {
    border-radius: 1rem;
    margin-bottom: 2.1rem;
    position: relative;
  }

  &__img-link {
    display: block;
  }

  &__picture {
    height: 20rem;
    border-radius: 1rem;

    img {
      border-radius: 1rem;
    }
  }

  &__date {
    font-size: 1.2rem;
    line-height: 2.7rem;
    font-weight: bold;
    color: $white;
    background: #6D7D95;
    border-radius: 15px;
    padding: 2px 3px;
    position: absolute;
    left: 1.5rem;
    top: 1.5rem;
    padding: 0 1.2rem;
    height: 2.7rem;
    z-index: 15;
  }

  &__header {
    font-size: 1.3rem;
    line-height: 1.6rem;
    font-weight: bold;
    margin-bottom: 1.7rem;
    display: block;
    transition: color .3s;

    @include hover {
      color: $green;
    }
  }

  &__desc {
    font-size: 1.2rem;
    line-height: 2rem;
    color: $primaryText
  }
}
