.card-article {
  $self: &;
  color: $primaryText;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;

  @include mobile {
    display: block;
  }

  &--big {
    flex-direction: row;

    @include mobile {
      display: block;
    }
  }

  &__img-col {
    display: block;

    #{$self}--big & {
      width: 45rem;

      @include desktop {
        width: 37rem;
      }

      @include smallDesktop {
        width: 31rem;
      }

      @include mobile {
        width: 100%;
      }
    }
  }

  &__img {
    width: 100%;
    height: 17rem;
    border-radius: 1.5rem 1.5rem 0 0;

    img {
      border-radius: 1.5rem 1.5rem 0 0;
    }

    #{$self}--big & {
      min-height: 36.1rem;
      height: 100%;
      border-radius: 1.5rem 0 0 1.5rem;

      @include mobile {
        height: 20rem;
        min-height: inherit;
        border-radius: 1.5rem 1.5rem 0 0;
      }

      img {
        border-radius: 1.5rem 0 0 1.5rem;

        @include mobile {
          border-radius: 1.5rem 1.5rem 0 0;
        }
      }
    }
  }

  &__content-col {
    padding: 2rem 3rem 2rem;
    border-radius: 0 0 1.5rem 1.5rem;
    background: $primaryBackground;
    flex: 1;

    @include mobile {
      padding: 2rem 1.5rem 3rem;
    }

    #{$self}--big & {
      padding: 3rem 4rem 2rem 3rem;
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;
      border-radius: 0 1.5rem 1.5rem 0;
      flex: 1;

      @include mobile {
        border-radius: 0 0 1.5rem 1.5rem;
        padding: 2rem 1.5rem 3rem;
      }
    }
  }

  &__header {
    display: block;
    font-size: 1.8rem;
    line-height: 2.7rem;
    font-weight: bold;
    margin-bottom: 1rem;
    transition: color .3s;

    @include hover {
      color: $green;
    }
  }

  &__desc {
    font-size: 1.2rem;
    line-height: 2rem;
    margin-bottom: 1.8rem;
  }

  &__date {
    font-size: 1.4rem;
    line-height: 1.7rem;

    #{$self}--big & {
      margin-top: auto;
    }
  }
}
