.page-404 {
  height: 101.7rem;
  background: #516570 no-repeat center bottom -3.6rem;
  text-align: center;
  color: $white;
  padding-top: 12.5rem;
  position: relative;

  @include mobile {
    height: 50rem;
    padding-top: 6.5rem;
  }

  &__picture {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 0;

    img {
      transform: translateY(-3.6rem)!important;

      @include mobile {
        transform: none!important;
      }
    }
  }

  &__container {
    position: relative;
    z-index: 5;
  }

  &__header {
    font-size: 3rem;
    line-height: 3.6rem;
    font-weight: bold;
    margin-bottom: 2rem;
  }

  &__desc {
    font-size: 1.8rem;
    line-height: 2.7rem;
    margin-bottom: 2.3rem;
  }

  &__btn {
    width: 21.7rem;
    height: 4.4rem;
    border-radius: .8rem;
    margin: 0 auto;
  }
}
