.file {
  display: inline-flex;
  flex-wrap: wrap;
  align-items: center;
  padding-left: .8rem;

  &__input {
    display: none;
  }

  &__icon {
    margin-right: .7rem;
    font-size: 1.2rem;
  }

  &__text {
    font-size: 2rem;
    color: #0056A2;

    @include mobile {
      font-size: 1.6rem;
      line-height: 1.8rem;
    }
  }
}
