.tag {
  $self: &;
  display: inline-flex;
  align-items: center;
  border-radius: 18.5px;
  font-size: 1.3rem;
  line-height: 1.6rem;
  color: $primaryText;
  padding: .7rem 1.5rem;
  transition: background .3s, color .3s, border-color .3s;
  position: relative;

  &.active {
    background: $lightBlue;
    color: $white;

    @include hover {
      color: $white
    }
  }

  @include hover {
    color: $green;
  }

  &__input {
    display: none;
  }
}
