.static-header {
  position: relative;
  padding: 5.4rem 0 6rem;
  position: relative;
  margin-bottom: 8.8rem;
  overflow: hidden;

  @include mobile {
    padding: 2rem 0rem;
    margin-bottom: 4rem;
  }

  .breadcrumbs {
    position: relative;
    z-index: 5;
  }

  &__header {
    font-size: 5rem;
    line-height: 6rem;
    font-weight: bold;
    color: $white;
    position: relative;
    z-index: 5;

    @include mobile {
      font-size: 3.6rem;
      line-height: 4rem;
    }

    &--select {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
    }
  }

  &__img-head {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    width: auto;
    height: 100%;
    text-align: right;

    img {
      height: 100%;
      width: auto;
    }
  }

  &__content {
    margin-top: 15.2rem;
    margin-bottom: -1rem;
    padding-top: 3.5rem;
    max-width: 74.1rem;
    border-top: 1px solid $white;
    padding-right: 11.3rem;
    position: relative;
    z-index: 5;

    @include mobile {
      margin-top: 3rem;
      padding-top: 2rem;
      padding-right: 0;
      margin-bottom: 0;
    }
  }

  &__desc {
    font-size: 3.6rem;
    line-height: 4.4rem;
    color: $white;
    margin-bottom: 1.2rem;

    @include mobile {
      font-size: 2rem;
      line-height: 2.3rem;
      margin-bottom: 1rem;
    }
  }

  &__price {
    font-size: 2rem;
    line-height: 3rem;
    color: $white;

    @include mobile {
      font-size: 1.4rem;
      line-height: 1.8rem;
    }
  }

  &__select-col {
    margin-left: auto;
  }

  &__select-list {
    display: flex;
    flex-wrap: wrap;
    margin: -.6rem;
    line-height: 0;
  }

  &__select-li {
    padding: .6rem;
  }
}
