.news-detail {
  color: $white;
  position: relative;
  padding-top: 5.4rem;
  padding-bottom: 15rem;
  background: $white;

  &:before {
    display: block;
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    height: 62.4rem;
    background: $darkBlue;

    @include lessVerticalTablet {
      height: 37.4rem;
    }
  }

  & > * {
    position: relative;
    z-index: 5;
  }

  &__header {
    font-size: 5rem;
    line-height: 6rem;
    font-weight: bold;
    margin-bottom: 3.8rem;

    @include lessVerticalTablet {
      font-size: $mobileMainHeader;
      margin-bottom: 1.8rem;
    }
  }

  &__content {
    width: 84.4rem;
    margin: 0 auto;

    @include lessHorizontalTablet {
      width: auto;
    }
  }

  &__date {
    font-size: 1.4rem;
    line-height: 1.7rem;
    margin-bottom: 1.9rem;

    @include lessVerticalTablet {
      font-size: $mobileText;
      margin-bottom: 0.9rem;
    }
  }

  &__title {
    font-size: 3.6rem;
    line-height: 4.4rem;
    font-weight: bold;
    margin-bottom: 4.1rem;

    @include lessVerticalTablet {
      font-size: $mobileSecondHeader;
      margin-bottom: 1.8rem;
    }
  }

  &__img-col {
    margin-bottom: 6rem;
  }

  &__picture {
    border-radius: 1.5rem;

    img {
      border-radius: 1.5rem;
    }
  }

  &__desc {
    font-size: 1.6rem;
    line-height: 2.7rem;
    color: $primaryText;
    margin-bottom: 5.9rem;

    &:last-child {
      margin-bottom: 0;
    }

    @include lessVerticalTablet {
      font-size: $mobileText;
      margin-bottom: 1.8rem;
    }
  }
}
