.tab-list {
  $self: &;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin: -.3rem;

  @include mobile {
    margin: 0;
    display: block;
    font-size: 0;
    white-space: nowrap;
  }

  &__li {
    padding: .3rem;

    @include mobile {
      padding: 0;
      display: inline-block;
    }
  }

  &__btn {
    font-size: 1.3rem;
    line-height: 1.6rem;
    font-weight: bold;
    color: $primaryText;
    padding: .7rem 1.6rem;
    border-radius: 18.5px;
    transition: background .3s, color .3s;

    #{$self}__li.active & {
      background: #56B4ED;
      color: $white;

      @include hover {
        background: $green;
        color: $white;
      }
    }

    @include hover {
      color: $green;
    }

    &:active {
      box-shadow: inset 0 0 5px 1px rgba($black, .3);
    }
  }
}
