.mobile-menu {
  $self: &;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  background: #204C59;
  padding: 1.3rem 1.5rem 0;
  overflow: auto;
  height: 100vh;
  display: none;

  &.active {
    display: block;
  }

  &:after {
    display: block;
    content: '';
    width: 100%;
    height: 8.2rem;
  }

  &__head-col {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 3.2rem;
  }

  &__close {
    line-height: 0;
    font-size: 2rem;
    color: #BAE3F1;
  }

  &__btn-col {
    margin-left: auto;
  }

  &__btn-list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: -.7rem;
  }

  &__btn-li {
    padding: .7rem;
    line-height: 0;
  }

  &__btn-icon {
    font-size: 2rem;
    color: #BAE3F1;
  }

  &__btn-form {
    line-height: 0rem;
  }

  &__counter {
    position: absolute;
    right: -.7rem;
    top: 0;
    padding: 0 .3rem;
    height: 1.6rem;
    background: $white;
    border-radius: .7rem;
    font-size: 1rem;
    line-height: 1.5rem;
    color: $black;
    @include sourceSans;
    transform: translateY(-50%);
  }

  &__phone-mob {
    display: block;
  }

  &__btn-form {

  }

  &__basket-mob {
    position: relative;
    line-height: 0;
    display: block;
  }

  &__type-col {
    display: inline-block;
    margin-bottom: 2rem;
    background: rgba($black, .3);
    border-radius: 16px;
  }

  &__type-list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  &__type-li {

  }

  &__type-link {
    display: block;
    padding: .7rem 2rem;
    border-radius: 16px;
    font-size: 1.2rem;
    color: rgba($white, .6);
    transition: color .3s, background .3s;

    #{$self}__type-li.active & {
      color: $black;
      background: $white;
    }
  }

  &__link-col {
    margin: 2rem 0 3rem;
  }

  &__link-list {
    margin: -.7rem;
  }

  &__link-li {
    padding: .7rem;
    position: relative;

    &:after, &:before {
      display: block;
      content: '';
      position: absolute;
      left: .7rem;
      right: .7rem;
      height: 1px;
      background: rgba($white, .2);
    }

    &:before {
      display: none;
    }

    &:first-child:before {
      bottom: 100%;
      display: block;
    }

    &:after {
      top: 100%;
    }
  }

  &__link {
    font-size: 1.3rem;
    line-height: 1.5rem;
    color: $white;
  }

  &__pay {
    margin: 3rem 0;
    background: #56B4ED;
    border-radius: .8rem;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding: 1rem;
  }

  &__pay-img {
    width: 4rem;
    height: 2.9rem;
    margin-right: 1.6rem;
  }

  &__pay-text {
    font-size: 1.3rem;
    color: $white;
  }
}
