.offers {
  padding: 11rem 0 15.6rem;
  background: $white;

  @include lessVerticalTablet {
    padding: 5rem 0;
  }

  .user-text p {
    color: $black;
    font-size: 1.6rem;
    line-height: 2.7rem;
    font-weight: 500;
    @include inter;

    @include lessVerticalTablet {
      font-size: $mobileText;
    }
  }

  &__content {
    display: flex;
    padding-top: 7.2rem;

    @include lessHorizontalTablet {
      display: block;
    }

    @include lessVerticalTablet {
      padding-top: 4rem;
    }
  }

  &__img-container {
    width: 50.6rem;
    height: 47.9rem;

    @include mobile {
      width: 100%;
      height: auto;
      margin: 0 auto;
      max-width: 40rem;
      margin-bottom: 4rem;
    }
  }

  &__img {
    width: 100%;
    height: auto;
  }

  &__list-col {
    flex: 1;
    padding-left: 4.9rem;
    padding-top: .4rem;

    @include mobile {
      padding-left: 0;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: -2.45rem -2.2rem;
  }

  &__li {
    padding: 2.45rem 2.2rem;
    width: calc(100% / 2);

    @include mobile {
      width: 100%;
    }
  }

  &__item {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    border-top: .3rem solid $primaryText;
    padding-top: 2.9rem;
  }

  &__icon-col {
    padding-top: .7rem;
  }

  &__icon {
  }

  &__item-text {
    color: $primaryText;
    font-size: 1.6rem;
    line-height: 2.1rem;
    font-weight: 400;
    @include inter;
    flex: 1;
    padding-left: 2.7rem;

    @include lessVerticalTablet {
      font-size: $mobileText;
    }
  }
}
