.history {
  $self: &;
  margin: 10.6rem 0 10.9rem;
  opacity: 0;

  &.init {
    opacity: 1;
  }

  @include mobile {
    margin: 3rem 0;
  }

  &__header {
    font-size: 2rem;
    line-height: 3rem;
    font-weight: bold;
    color: $black;
    margin-bottom: 3.2rem;
  }

  &__content {
    background: linear-gradient(180deg, #2175AA 0%, #12A1D9 23.95%, #56C3E6 58.18%, #12A1D9 68.59%, #2175AA 100%);
    border-radius: 1rem;
    padding: 2.7rem 5rem 5.2rem;

    @include mobile {
      padding: 2rem 1.5rem;
    }
  }

  &__head {
    margin-bottom: 3.3rem;
  }

  &__msg {
    padding: 3.5rem 2.8rem;
    font-size: 1.2rem;
    line-height: 2rem;
    color: $primaryText;
    border-radius: 15px;
    box-shadow: 0px 23px 25px rgba(0, 0, 0, 0.1);
    margin: 0 auto;
    margin-bottom: 1.8rem;
    width: 45.4rem;
    background: $white;
    transform: translateX(calc(50% - 4.4rem));
    position: relative;

    @include mobile {
      width: 100%;
      transform: none;
      padding: 2rem 1.5rem;
    }

    &:before {
      display: block;
      content: '';
      width: 2.8rem;
      height: .9rem;
      background: url(../img/css/triangle-bot.png) no-repeat center;
      background-size: 100% auto;
      position: absolute;
      left: 3rem;
      top: calc(100% - 1px);

      @include mobile {
        transform: translateX(-50%);
        left: 50%;
      }
    }
  }

  &__line {
    position: relative;

    &:before {
      display: block;
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      background: rgba($white, .2);
      height: 1px;
    }
  }

  &__circle {
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    background: $white;
    position: relative;
    z-index: 5;
    margin: 0 auto;
  }

  &__slide {
    padding-left: 8rem;
    cursor: pointer;

    @include mobile {
      padding-left: 2rem;
    }

    &:first-child {
      padding-left: 0;
    }
  }

  &__title {
    font-size: 1.2rem;
    line-height: 1.8rem;
    color: #bae3f1;
    margin-bottom: 2.1rem;
    width: 12rem;
    transition: color .3s;

    @include mobile {
      font-size: 1rem;
      line-height: 1.4rem;
      margin-bottom: 1.2rem;
      width: 10rem;
    }

    #{$self}__slide.slick-current & {
      color: $white;
    }

    #{$self}__slide:hover & {
      @include desktop {
        color: $white;
      }
    }
  }

  &__date {
    font-size: 1.8rem;
    line-height: 2.7rem;
    color: #bae3f1;
    transition: color .3s;

    @include mobile {
      font-size: 1.4rem;
      line-height: 1.8rem;
    }

    #{$self}__slide.slick-current & {
      color: $white;
    }

    #{$self}__slide:hover & {
      @include desktop {
        color: $white;
      }
    }
  }
}
