.card-vacance {
  padding-top: 5rem;
  border-top: 1px solid $primaryText;

  @include mobile {
    padding-top: 2.5rem;
  }

  &__head-col {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 3.4rem;

    @include mobile {
      margin-bottom: 1.5rem;
      align-items: flex-start;
    }
  }

  &__header {
    flex: 1;
    padding-right: 2rem;
    font-size: 2rem;
    line-height: 3rem;
    font-weight: bold;
    color: $lightBlue;
    display: block;
    transition: color .3s;

    @include mobile {
      font-size: 1.6rem;
      line-height: 2rem;
      padding-right: 2rem;
    }

    @include hover {
      color: $green;
    }
  }

  &__price {
    font-size: 2rem;
    line-height: 3rem;
    color: $black;
    font-weight: bold;

    @include mobile {
      font-size: 1.6rem;
      line-height: 1.8rem;
    }
  }

  &__desc {
    font-size: 2rem;
    line-height: 3rem;
    color: $primaryText;

    @include mobile {
      font-size: 1.4rem;
      line-height: 1.8rem;
    }
  }

  &__list-col {

  }

  &__list {

  }

  &__li {
  }

  &__text {
    font-size: 2rem;
    line-height: 3rem;
    color: $primaryText;

    @include mobile {
      font-size: 1.4rem;
      line-height: 1.8rem;
    }

    &:before {
      display: inline;
      content: '-';
      margin-right: .5rem;
    }
  }
}
