.form-checkbox {
  $self: &;
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  &__checkbox {
    display: none;
  }

  &__icon-col {
    font-size: 1.3rem;
    line-height: 0;
    width: 2.2rem;
    height: 2.2rem;
    border: 2px solid $darkBlue;
    border-radius: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background .3s;

    #{$self}__checkbox:checked + & {
      background: #E7ECF5;

      .form-checkbox {
        &__icon {
          opacity: 1;
        }
      }
    }
  }

  &__icon {
    opacity: 0;
    transition: opacity .3s;
  }

  &__desc {
    font-size: 1.1rem;
    line-height: 1.6rem;
    color: $darkBlue;
    flex: 1;
    padding-left: .8rem;
  }
}
