.info-list {
  margin-bottom: 9rem;
  padding-left: 6rem;

  @include lessVerticalTablet {
    margin-bottom: $mobileVerticalArea;
    padding: 0;
  }

  &__list {
    margin: -1.5rem;
  }

  &__li {
    padding: 1.5rem;
  }

  &__item {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    font-size: 2rem;
    line-height: 3rem;
    color: $black;
  }

  &__key {
    font-weight: bold;
    width: 28.8rem;
    padding-right: 2rem;

    @include lessVerticalTablet {
      width: 100%;
      padding-right: 2rem;
      font-size: $mobileText;
    }
  }

  &__value {
    flex: 1;

    @include lessVerticalTablet {
      font-size: $mobileText;
    }
  }
}
