.channels {
  $self: &;
  background: $white;
  padding-bottom: 16.1rem;

  &__title {
    font-size: 3.6rem;
    line-height: 4.4rem;
    color: $black;
    font-weight: bold;
    margin-bottom: 3rem;
    max-width: 68rem;

    @include mobile {
      font-size: 2rem;
      line-height: 2.3rem;
      margin-bottom: 1.5rem;
    }
  }

  &__desc {
    font-size: 2rem;
    line-height: 3rem;
    color: $primaryText;
    margin-bottom: 8rem;
    max-width: 90rem;

    @include mobile {
      font-size: 1rem;
      line-height: 1.5rem;
      margin-bottom: 4rem;
    }
  }

  &__tag-col {
    max-width: 79.4rem;
  }

  &__tag-list {
    display: flex;
    flex-wrap: wrap;
    margin: -.5rem;

    #{$self}__tag-col--level2 & {
      display: inline-flex;
    }
  }

  &__tag-li {
    padding: .5rem;
  }

  &__tag {
    #{$self}__tag-li.active & {
      background: $lightBlue;
      color: $white;
    }
  }

  &__tab-li {
    display: none;

    &.active {
      display: block;
    }
  }

  &__card-col {
    margin-top: 5.4rem;
    margin-bottom: 7rem;

    @include mobile {
      margin-top: 3rem;
      margin-bottom: 3.5rem;
    }
  }

  &__card-list {
    display: flex;
    flex-wrap: wrap;
    margin: -.8rem;

    @include mobile {
      margin: -.4rem;
    }
  }

  &__card-li {
    padding: .8rem;
    width: calc(100% / 4);

    @include mobile {
      padding: .4rem;
      width: calc(100% / 2);
    }

    #{$self}__card-col.column & {
      width: 50%;

      @include mobile {
        width: 100%;
      }

      .card-channel {
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        padding-left: 3.7rem;

        @include mobile {
          padding-left: 1rem;
          padding-right: 1rem;
        }

        &__img-col {
          margin: 0;
        }

        &__content {
          flex: 1;
          padding-left: 4rem;
          text-align: left;
        }
      }
    }
  }

  &__btn-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    margin: -1rem;
  }

  &__btn-li {
    padding: 1rem;
  }

  &__bot-col {
    padding-top: 3rem;
    border-top: 1px solid #204C59;
  }

  &__btn-show-more {
    width: 19.6rem;
    height: 3.7rem;
    border-radius: 18.5px;
    font-size: 1.3rem;
  }

  &__btn-download {
    height: 3.7rem;
    border-radius: 18.5px;
    font-size: 1.3rem;
  }
}
