.breadcrumbs {
  margin-bottom: 1.1rem;
  $self: &;

  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: -1rem;
  }

  &__li {
    padding: 1rem;
  }

  &__item {
    position: relative;
    line-height: 0;
  }

  &__link {
    font-size: 1.3rem;
    line-height: 1.6rem;
    color: $white;
    transition: color .3s;

    @include hover {
      color: $green;
    }

    #{$self}--black & {
      color: $primaryText;

      @include hover {
        color: $green;
      }
    }

    #{$self}__li:last-child & {
      color: rgba($white, .5);
    }
  }

  &__arrow {
    position: absolute;
    left: calc(100% + .7rem);
    top: 50%;
    transform: translate(0%, -50%);
    color: rgba($white, .3);
    font-size: 1rem;

    #{$self}__li:last-child & {
      display: none;
    }

    #{$self}--black & {
      color: rgba($primaryText, .3);
    }
  }
}
