.sales {
  $self: &;
  background: $white;
  overflow: hidden;
  padding: $mainPageSectionGap;

  @include mobile {
    padding: 4rem 0;
  }

  &--blue {
    background: $primaryBackground;

    .card-sales {
      background: $white;
    }
  }

  &__section-header {
    margin-bottom: 4.5rem;

    @include mobile {
      margin-bottom: 3.8rem;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: -1rem;

    @include mobile {
      display: block;
      margin: -.7rem;
    }
  }

  &__li {
    padding: 1rem;
    width: 50%;

    @include mobile {
      width: 100%;
      padding: .7rem;
    }
  }

  .section-header__btn {
    width: 13rem;
    height: 3.7rem;
  }

  .section-header {
    margin-bottom: 4.5rem;
  }
}
