.documents {
  background: $white;
  padding-bottom: 17rem;

  @include mobile {
    padding-bottom: 6rem;
  }

  &__header {
    font-size: 3.6rem;
    line-height: 4.4rem;
    font-weight: bold;
    margin-bottom: 3rem;
    color: $black;

    @include mobile {
      font-size: 2.4rem;
      line-height: 2.8rem;
      margin-bottom: 1.5rem;
    }
  }

  &__desc {
    font-size: 2rem;
    line-height: 3rem;
    color: $primaryText;
    margin-bottom: 6.2rem;

    @include mobile {
      font-size: 1.4rem;
      line-height: 1.8rem;
      margin-bottom: 3rem;
    }
  }

  &__tag-col {
    margin-bottom: 4.5rem;
    padding-top: 2.5rem;
    border-top: 1px solid $primaryText;

    @include mobile {
      margin-bottom: 2rem;
      padding-top: 1.5rem;
    }
  }

  &__tag-list {
    display: flex;
    flex-wrap: wrap;
    margin: -.7rem;
  }

  &__tag-li {
    padding: .7rem;

    &.active {
      .tag {
        background: $lightBlue;
        color: $white;
        padding: .7rem 1.5rem;

        @include hover {
          color: $white
        }
      }
    }
  }

  &__doc-col {

  }

  &__accordion-list {

  }

  &__accordion-li {
    display: none;

    &.active {
      display: block;
    }
  }

  &__doc-list {
    display: flex;
    flex-wrap: wrap;
    margin: -2rem;

    @include mobile {
      display: block;
      margin: -1rem;
    }
  }

  &__doc-li {
    padding: 2rem;
    width: 50%;

    @include mobile {
      padding: 1rem;
      width: 100%;
    }
  }

  &__doc-item {
    border: 1px solid rgba($primaryText, .5);
    border-radius: 1rem;
    padding: 4.4rem 2rem 5.5rem 7rem;
    display: flex;
    flex-wrap: wrap;
    transition: border-color .3s;

    @include mobile {
      padding: 2rem 1rem;
    }

    @include hover {
      border-color: $green;
    }
  }

  &__doc-icon {
    width: 7.2rem;
    height: 8.6rem;

    @include mobile {
      width: 5rem;
      height: 5rem;
    }
  }

  &__content-col {
    flex: 1;
    padding-left: 2rem;
  }

  &__item-desc {
    font-size: 1.6rem;
    line-height: 2.4rem;
    font-weight: 500;
    color: $primaryText;
    margin-bottom: 1.2rem;

    @include mobile {
      font-size: 1.4rem;
      line-height: 1.8rem;
      margin-bottom: .8rem;
    }
  }

  &__file-spec {
    font-size: 1.4rem;
    line-height: 2.1rem;
    color: #6D7D95;

    @include mobile {
      font-size: 1.2rem;
      line-height: 1.5rem;
    }
  }
}
