.warning {
  $self: &;
  background: #CF3434;
  color: $white;
  padding: 1.4rem 0 1.7rem;
  position: sticky;
  left: 0;
  top: 0;
  z-index: 1000;
  display: none;

  @include mobile {
    padding: 1rem 0;
  };

  &.active {
    display: block;
  }

  &.hide {
    display: none;
  }

  &__container {
    padding-left: 19rem;
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    @include mobile {
      padding-left: 1.5rem;
    };
  }

  &__desc {
    flex: 1;
    padding-right: 2rem;
    font-size: 2.2rem;
    line-height: 2.7rem;
    font-weight: bold;
    transition: color .3s;

    @include mobile {
      font-size: 1.8rem;
      line-height: 2rem;
      padding-right: 1rem;
    };

    @include hover {
      color: #FBAD1D;
    };
  }

  &__close {
    width: 2.8rem;
    height: 2.8rem;
    border: 1px solid rgba($white, .4);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    line-height: 0;
    transition: border-color .3s;

    @include hover {
      border-color: $white;
    };
  }

  &__icon {
    font-size: 1.4rem;
    color: $white;
  }
}
