.tariffs {
  $self: &;
  padding: $mainPageSectionGap;
  background: $primaryBackground;

  @include mobile {
    padding-top: 3.9rem;
    padding-bottom: 6.6rem;
  }

  &__container {
    max-width: 123rem;

    @include lessFullHd {
      max-width: 113.6rem;
    }
  }

  &__content-list {
    box-shadow: 0px 20px 55px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
  }

  &__content-li {
    display: none;

    &.active {
      display: block;
    }
  }

  &__tab-col {
    margin-bottom: 4.7rem;

    @include mobile {
      overflow: auto;
      padding-bottom: 1rem;
      margin-bottom: calc(2.2rem - 1rem);
    }
  }

  &__card {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;

    @include mobile {
      display: block;
    }

    @include hover {
      .arrow {
        opacity: 1;
      }
    };
  }

  &__img-col {
    background: #F7A202;
    border-radius: 10px 0 0 10px;
    width: 78.9rem;
    transition: background .3s;

    @include desktop {
      width: 60rem;
    }

    @include smallDesktop {
      width: 55rem;
    }

    @include mobile {
      width: 100%;
      border-radius: 10px 10px 0 0;
      overflow: hidden;
    }
  }

  &__img-list {
    height: 100%;
  }

  &__img-li {
    display: none;
    height: 100%;

    &.active {
      display: block;
    }
  }

  &__img-item {
    height: 100%;
    padding: 2.3rem 0rem 1rem 2.3rem;
    position: relative;

    @include mobile {
      padding: 1.2rem 0rem 1.5rem 1.5rem;
    }

    & > * {
      position: relative;
      z-index: 5;
    }
  }

  &__picture {
    position: absolute;
    right: 0;
    bottom: 0;
    width: auto;
    z-index: 0!important;
    max-width: 37.2rem;
    max-height: 100%;

    @include mobile {
      transform: translate(10%, 0);
      max-width: 30rem;
    }

    img {
    }
  }

  &__icon-col {
    width: 7.4rem;
    height: 7.4rem;
    margin-bottom: 2rem;

    @include mobile {
      width: 3.6rem;
      height: 3.6rem;
      margin-bottom: 1.8rem;
    }
  }

  &__spec-col {
    margin-bottom: 2rem;

    @include mobile {
      margin-bottom: 1.3rem;
      max-width: 14rem;
    }
  }

  &__spec-list {

  }

  &__spec-li {

  }

  &__spec-item {
    font-size: 1.3rem;
    line-height: 2.2rem;
    color: $white;

    @include mobile {
      font-size: 1rem;
      line-height: 1.5rem;
    }
  }

  &__spec-key {

  }

  &__spec-value {

  }

  &__btn-join {
    display: inline-flex;
    width: auto;
    height: 4rem;
    margin-bottom: 2.5rem;
    border: 1px solid $white;
    padding: 0 1.1rem;
    background: #EFF3F9;
    border-color: #EFF3F9;

    @include mobile {
      height: 3.2rem;
      font-size: 1rem;
      margin-bottom: 1.5rem;
      padding: 0 1.5rem;
      box-shadow: none!important;
    }

    @include hover {
      background: #EFF3F9;
      border-color: $green;
      color: $primaryText;
    }

    .btn__animate-col {
      position: relative;
    }

    .btn__icon {
      font-size: 2.8rem;

      &--animate {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        transition: .3s;
      }
    }
  }


  &__type-col {
    flex: 1;
    border-radius: 0 10px 10px 0;
    border: 1px solid rgba(#204C59, .1);
    position: relative;
    z-index: 5;

    @include smallDesktop {
      width: 40.5rem;
    }

    @include mobile {
      border-radius: 0 0 10px 10px;
      width: 100%;
    }
  }

  &__type-list {
    height: 100%;
    display: flex;
    flex-direction: column;
  }

  &__type-li {
    flex: 1;
  }

  &__type-item {
    height: 100%;
    padding: 2rem 2rem 2.4rem 2.4rem;
    border: 1px solid #F7A202;
    border-width: 1px 0 0 0;
    background: $white;
    position: relative;
    transition: background .3s, border-color .3s;
    cursor: pointer;

    @include mobile {
      padding: 1.5rem;
    }

    @include hover {
      background: #EFF2F8;
    }

    #{$self}__type-li:first-child & {
      border: none;
      border-radius: 0 10px 0 0;

      @include mobile {
        border-radius: 0;
      }
    }

    #{$self}__type-li:last-child & {
      border-radius: 0 0 10px 0;

      @include mobile {
        border-radius: 0 0 10px 10px;
      }
    }

    #{$self}__type-li.active & {
      background: #EFF2F8;
    }
  }

  &__type-content-col {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 1.1rem;

    @include mobile {
      margin-bottom: .5rem;
    };
  }

  &__bottom-col {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  &__type-header {
    font-size: 1.6rem;
    line-height: 1.8rem;
    color: $primaryText;
    font-weight: bold;
    flex: 1;
    padding-right: 2rem;
    transition: color .3s;

    @include mobile {
      font-size: 1.2rem;
      line-height: 1.5rem;
    }

    #{$self}__type-item:hover & {
      @include fromDesktop {
        color: $green;
      }
    }
  }

  &__type-desc {
    font-size: 1.2rem;
    line-height: 1.8rem;
    color: $primaryText;
    flex: 1;
    padding-right: 1rem;

    @include mobile {
      font-size: 1rem;
      line-height: 1.5rem;
    }
  }

  &__type-price {
    font-size: 1.6rem;
    line-height: 1.8rem;
    text-align: right;
    color: $primaryText;
    font-weight: bold;
  }

  &__type-img-col {

  }

  &__type-icon {
    font-size: 3.3rem;
    opacity: 0;
    transition: opacity .3s;

    #{$self}__type-li.active & {
      opacity: 1;
    }

    @include mobile {
      font-size: 2.8rem;
    }
  }

  &__type-checkbox {
    display: none;
  }

  &__slider-title {
    font-size: 1.3rem;
    line-height: 2.2rem;
    color: $white;
    margin-bottom: 1.5rem;
  }

  .tariff-slider {
    margin-left: -2.3rem;
    width: calc(100% + 4.6rem);

    @include mobile {
      margin-left: -1.5rem;
      width: calc(100% + 3rem);
    }
  }

  &__service-header, &__service-price {
    font-size: 1.8rem;
    line-height: 2.2rem;
    color: $white;
    font-weight: bold;
  }

  &__service-price {
    margin-bottom: .8rem;
  }

  &__special {
    max-width: 28rem;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 2.3rem;
  }

  &__special-icon {
    font-size: 3.3rem;
  }

   &__special-desc {
     flex: 1;
     padding-left: .5rem;
     font-size: 1.3rem;
     line-height: 1.9rem;
     color: $white;
     font-weight: 500;
   }
}
