.basket {
  padding-left: 1.8rem;
  display: none;

  @include mobile {
    padding-left: 0;
  }

  &.active {
    display: block;
  }

  &:after {
    display: block;
    content: '';
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 10px 12px 10px 0;
    border-color: transparent #ffffff transparent transparent;
    position: absolute;
    left: .7rem;
    bottom: 2rem;
    z-index: 50;

    @include mobile {
      display: none;
    }
  }

  &__container {
    padding: 3.4rem;
    background: $white;
    box-shadow: 0px 10px 45px rgba(0, 0, 0, 0.1);
    border-radius: 1.5rem;
    max-width: 52.4rem;
    min-width: 52.4rem;
    color: $black;
    height: 100%;

    @include mobile {
      max-width: 100%;
      min-width: 100%;
      padding: 2rem 1.6rem;
      padding-top: 6rem;
      border-radius: 0;
    }
  }

  &__list-col {
    overflow: auto;
    overflow-x: hidden;
    max-height: 24rem;
    margin-right: -2rem;
    padding-right: 2rem;
    margin-bottom: 2.6rem;

    @include mobile {
      max-height: 27rem;
    }
  }

  &__list {
    margin: -1.2rem;
  }

  &__li {
    padding: 1.2rem;
    position: relative;

    &:after {
      display: block;
      content: '';
      height: 1px;
      position: absolute;
      left: 1.2rem;
      right: 1.2rem;
      top: 100%;
      background: $primaryText;
    }

    &:last-child:after {
      display: none;
    }
  }

  &__overflow-container {
    overflow: hidden;
    padding-bottom: 1rem;
    border-bottom: 1px solid rgba($primaryText, .3);
  }

  &__header {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 1.4rem;
  }

  &__header-text {
    font-size: 1.8rem;
    line-height: 2.2rem;
    font-weight: bold;
    flex: 1;
    padding-right: 2rem;

    @include mobile {
      font-size: 1.6rem;
      line-height: 1.9rem;
      padding-right: 1rem;
    }
  }

  &__header-price {
    font-size: 1.8rem;
    line-height: 2.2rem;
    font-weight: bold;

    @include mobile {
      font-size: 1.5rem;
      line-height: 1.8rem;
    }
  }

  &__close {
    font-size: 1.9rem;
    line-height: 0;
    color: $primaryBackground;
    margin-left: 1.8rem;
    transition: color .3s;

    @include mobile {
      font-size: 1.4rem;
    }

    @include hover {
      color: $green;
    }
  }

  &__sub-list-col {

  }

  &__sub-list {
    margin: -1rem;
  }

  &__sub-li {
    padding: 1rem;
    position: relative;

    &:after {
      display: block;
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      top: 100%;
      background: rgba($primaryText, .3);
      height: 1px;
    }

    &:last-child:after {
      display: none;
    }
  }

  &__item {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  &__title {
    font-size: 1.3rem;
    line-height: 2rem;
    font-weight: 500;
    flex: 1;
    padding-right: 1rem;

    @include mobile {
      font-size: 1.1rem;
      line-height: 1.4rem;
    }
  }

  &__price {
    font-size: 1.3rem;
    line-height: 2rem;
    font-weight: 500;
    width: 9.2rem;
    text-align: right;

    @include mobile {
      font-size: 1rem;
      line-height: 1.3rem;
      width: auto;
    }
  }

  &__bottom {
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    @include mobile {
      display: block;
    }
  }

  &__bottom-header {
    font-size: 1.8rem;
    line-height: 2.2rem;
    font-weight: bold;
    flex: 1;
    padding-right: 2rem;

    @include mobile {
      font-size: 1.6rem;
      line-height: 1.9rem;
      padding-right: 0;
      flex: none;
      margin-bottom: 2rem;
    }
  }

  &__bottom-btn.btn {
    width: 15.6rem;
    height: 4.4rem;

    @include mobile {
      width: 100%;
    }
  }

  &__btn-close {
    display: none;
    position: absolute;
    right: 1rem;
    top: 1rem;
    font-size: 2.5rem;
    color: $primaryBackground;

    @include mobile {
      display: block;
    }
  }
}
