.pay-info {
  margin-bottom: 12rem;

  @include mobile {
    margin-bottom: 4rem;
  };

  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    margin: -2.2rem;

    @include mobile {
      margin: -2.2rem -1rem;
    };
  }

  &__li {
    padding: 2.2rem;
    width: calc(100% / 5);

    @include smallDesktop {
      width: calc(100% / 3);
    };

    @include mobile {
      width: calc(100% / 2);
      padding: 2.2rem 1rem;
    };
  }

  &__card {
    height: 100%;
    display: flex;
    flex-direction: column;
  }

  &__content {
    flex: 1;
    display: flex;
    flex-direction: column;
  }

  &__header {
    font-size: 1.4rem;
    line-height: 2.4rem;
    color: #3A5565;
    flex: 1;
  }

  &__join {
    font-size: 1.3rem;
    line-height: 1.9rem;
    color: rgba(255, 0, 0, .5);
    margin-top: 1.4rem;
  }

  &__bottom {
    border-top: 1px solid rgba($primaryText, .3);
    padding-top: 2rem;
    margin-top: 1.7rem;
  }

  &__price {
    background: $lightBlue;
    border-radius: 18.5px;
    padding: 0 2.3rem;
    text-align: center;
    height: 3.7rem;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-size: 1.3rem;
    line-height: 2rem;
    color: $white;
    font-weight: bold;
  }
}
