.popup-channel {
  $self: &;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(180deg, rgba(32, 76, 89, 0.85) 29.98%, rgba(32, 76, 89, 0) 100%);
  z-index: 999;
  opacity: 0;
  pointer-events: none;
  display: flex;
  justify-content: center;
  align-items: center;

  @include mobile {
  }

  &.active {
    opacity: 1;
    pointer-events: auto;
  }

  &__container {
    max-width: 83.4rem;
    margin: 0 auto;
    background: $white;
    border-radius: 2rem;
    padding: 5rem 5rem 0;
    height: 76rem;
    overflow: auto;
    max-height: 90vh;
    position: relative;

    @include mobile {
      padding: 2rem;
      height: 100vh;
      width: 100vw;
      max-height: none;
      border-radius: 0;
    }

    &:after {
      display: block;
      content: '';
      height: 5rem;
      width: 100%;

      @include mobile {
        height: 2rem;
      }
    }
  }

  &__close {
    display: block;
    font-size: 4.8rem;
    line-height: 0;
    margin-left: auto;
    margin-bottom: .5rem;
    color: #E7ECF5;
    transition: color .3s;
    position: sticky;
    right: 0rem;
    top: 0rem;

    @include hover {
      color: rgba($green, .7);
    }

    @include mobile {
      font-size: 3rem;
    }
  }

  &__img-col {
    width: 19.6rem;
    height: 19.6rem;
    background: #FFFFFF;
    box-shadow: 0px 16px 52px rgba(32, 76, 89, 0.1);
    border-radius: 3rem;
    margin-bottom: 6.4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    overflow: hidden;
    opacity: 0;
    transition: opacity .3s;

    #{$self}.active & {
      opacity: 1;
    }

    @include mobile {
      width: 14rem;
      height: 14rem;
      margin-bottom: 2rem;
    }
  }

  &__header {
    display: flex;
    align-items: center;
    padding-bottom: 2.9rem;
    border-bottom: 1px solid $primaryText;
    margin-bottom: 4rem;

    @include mobile {
      margin-bottom: 2rem;
      padding-bottom: 1rem;
    }
  }

  &__header-text {
    font-size: 3rem;
    line-height: 3.6rem;
    color: $black;
    font-weight: bold;
    margin-right: 2.5rem;

    @include mobile {
      font-size: 1.8rem;
      line-height: 2.1rem;
    }
  }

  &__number {
    background: $primaryBackground;
    border-radius: 18.5px;
    height: 3rem;
    padding: 0 1.6rem;
    font-size: 1.3rem;
    line-height: 1.6rem;
    color: $primaryText;
    font-weight: bold;
    display: flex;
    align-items: center;

    @include mobile {
      font-size: 1.1rem;
      line-height: 1.3rem;
    }
  }

  &__number-text {
    margin-left: .3rem;
  }

  &__desc {
    font-size: 2rem;
    line-height: 3rem;
    color: $primaryText;
    padding-bottom: 4rem;
    border-bottom: 1px solid $primaryText;

    @include mobile {
      font-size: 1.4rem;
      line-height: 1.8rem;
      padding-bottom: 2rem;
      padding-bottom: 1rem;
    }
  }

  &__title-desc {
    font-size: 1.2rem;
    line-height: 2rem;
    color: rgba($primaryText, .8);
    margin-bottom: .5rem;
  }
}
