.login-form {
  &__header {
    font-size: 1.3rem;
    line-height: 1.6rem;
    color: $black;
    font-weight: bold;
    margin-bottom: 1.7rem;
  }

  &__list {
    margin: -.8rem;
  }

  &__li {
    padding: .8rem;
  }

  &__list-col {
    margin-bottom: 2.4rem;
  }

  .btn {
    border-radius: 1rem;
    width: 10.5rem;
    height: 3.5rem;
    font-size: 1.3rem;
  }
}
