.payment-info {
  padding-left: 12rem;
  margin-bottom: 6rem;

  @include mobile {
    padding: 0;
    margin-bottom: 3rem;
  }

  &__container {
    display: flex;
    flex-wrap: wrap;

    @include lessVerticalTablet {
      display: block;
      padding-top: 4.4rem;
    }
  }

  &__header {
    font-size: 2rem;
    line-height: 3rem;
    font-weight: bold;
    color: $black;
  }

  &__content {
    flex: 1;
    padding-left: 5.2rem;

    @include lessVerticalTablet {
      padding-left: 0;
    }
  }

  &__list {
    margin: -.2rem;
  }

  &__li {
    padding: .2rem;
  }

  &__item {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    font-size: 2rem;
    line-height: 3.4rem;
    color: $primaryText;

    @include lessVerticalTablet {
      font-size: $mobileSecondHeader;
    }
  }

  &__key {
    color: rgba($primaryText, .7);
    margin-right: .5rem;

    @include lessVerticalTablet {
      margin-right: 0;
      font-size: $mobileText;
    }
  }

  &__value {
    @include lessVerticalTablet {
      font-size: $mobileText;
    }
  }
}
