.partners {
  margin-bottom: 19rem;

  @include mobile {
    margin-bottom: 8rem;
  }

  &__list-col {

  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: -.8rem;
  }

  &__li {
    padding: .8rem;
    width: calc(100% / 4);

    @include smallDesktop {
      width: calc(100% / 3);
    }

    @include mobile {
      width: calc(100% / 2);
    }
  }
}
