.product-animate {
  font-size: 5rem;
  position: absolute;
  z-index: 999;
  left: 50%;
  top: 50%;
  opacity: 0;
  border-radius: 50%;

  @include mobile {
    display: none;
    font-size: 3rem;
  };

  &--mobile {
    display: none;

    @include mobile {
      display: block;
    };
  }
}
