.card-licence {
  border-radius: 1rem;
  border: 1px solid rgba($primaryText, .5);
  padding: 6rem 6rem 3.7rem 6rem;
  height: 100%;
  display: block;
  $self: &;
  transition: border-color .3s;

  @include mobile {
    padding: 2rem 1.5rem;
  }

  @include hover {
    border-color: $green;
  }

  &__img {
    max-width: 24rem;
    margin: 0 auto;
    margin-bottom: 3rem;
    border-radius: 1.5rem;

    @include mobile {
      margin: 0;
      margin-bottom: 1.5rem;
      height: 15rem;
      width: auto;
      border-radius: none;
    }
  }

  &__text {
    font-size: 1.8rem;
    line-height: 2.7rem;
    color: $primaryText;

    @include mobile {
      font-size: 1.2rem;
      line-height: 1.5rem;
    }
  }
}
