.vacance {
  background: $white;
  padding-bottom: 35.7rem;

  @include mobile {
    padding-bottom: 6rem;
  }

  &__header {
    font-size: 3.6rem;
    line-height: 4.4rem;
    color: $black;
    font-weight: bold;
    margin-bottom: 3rem;

    @include mobile {
      font-size: 3rem;
      line-height: 3.3rem;
      margin-bottom: 1.5rem;
    }
  }

  &__desc {
    font-size: 2rem;
    line-height: 3rem;
    color: $primaryText;
    margin-bottom: 11.8rem;

    @include mobile {
      font-size: 1.4rem;
      line-height: 1.8rem;
      margin-bottom: 4rem;
    }
  }

  &__list {
    margin: -2.5rem;

    @include mobile {
      margin: -1rem;
    }
  }

  &__li {
    padding: 2.5rem;

    @include mobile {
      padding: 1rem;
    }

    &:last-child {
      .card-vacance {
        padding-bottom: 5rem;
        border-bottom: 1px solid $primaryText;

        @include mobile {
          padding-bottom: 2.5rem;
        }
      }
    }
  }
}
