*,
*:before,
*:after {
  box-sizing: border-box;
  outline: none!important;
  margin: 0;
  padding: 0;
}

html,
body {
  font-size: 62.5%;
}

body {
  line-height: 1.5;
  @include inter;

  &.overflow {
    overflow: hidden;
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
  }
}

p {
  margin: 0;
}

button {
  cursor: pointer;
  background: none;
  padding: 0;
  border: none;
  color: currentColor;
}

img {
  height: auto;
  max-width: 100%;
  max-height: 100%;
  vertical-align: bottom;
}

label {
  cursor: pointer;
  user-select: none;
}

textarea {
  resize: vertical;
}

ul {
  padding: 0;
  margin: 0;
}

li {
  list-style-type: none;
}

input[placeholder] {
  text-overflow: ellipsis;
  white-space: nowrap;
}

:focus::-webkit-input-placeholder {
  color: transparent;
}

:focus::-moz-placeholder {
  color: transparent;
}

:focus:-moz-placeholder {
  color: transparent;
}

:focus:-ms-input-placeholder {
  color: transparent;
}

.responsive-video {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;

  iframe,
  object,
  embed {
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
  }
}

button,
input,
optgroup,
select,
textarea {
  @include inter;
  box-shadow: none;
}

input {
  border: none;
}

a {
  text-decoration: none;
  color: currentColor;
}

.site-wrapper {
  display: flex;
  flex-direction: column;
  font-size: 1.6rem;
  min-height: 100vh;

  &--visible {
    overflow-x: visible;
  }
}

.site-content {
  flex: 1 0 auto;
  overflow: hidden;
}

.truncate {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.picture {
  display: block;
  overflow: hidden;
  @include safariOverflowFix;

  &--cover {
    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
      transition: transform .3s;

      &:hover {
        @include fromDesktop {
          transform: scale3d(1.1, 1.1, 1.1);
        }
      }
    }
  }

  &--anim-off {
    img {
      &:hover {
        @include fromDesktop {
          transform: none;
        }
      }
    }
  }

  &--anim {
    img {
      transition: transform .3s;

      &:hover {
        @include fromDesktop {
          transform: scale3d(1.1, 1.1, 1.1);
        }
      }
    }
  }
}

section .container, .page-header .container, .page-footer .container {
  @include asideDesktop {
    padding-left: $asidePadding!important;
  }

  @include mobile {
    padding-left: 1.5rem!important;
  }
}
