.pay-var {
  $self: &;

  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    align-content: stretch;
    margin: -1rem;

    @include mobile {
      margin: -.4rem;
    }
  }

  &__li {
    padding: 1rem;
    width: 18.3554%;

    @include desktop {
      width: calc(100% / 3);
    }

    @include mobile {
      width: 50%;
      padding: .4rem;
    }

    &:nth-child(1), &:nth-child(2) {
      width: 22.4669%;

      @include desktop {
        width: calc(100% / 3);
      }

      @include mobile {
        width: 50%;
      }
    }
  }

  &__item {
    display: block;
    width: 100%;
    background: #EFF3F9;
    border: 1px solid rgba(32, 76, 89, 0.05);
    border-radius: 10px;
    padding: 3rem 2rem;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: space-between;
    transition: border-color .3s;

    @include hover {
      border-color: $green;
    }

    #{$self}__li.active & {
      background: #2175AA;
    }
  }

  &__img-col {
    margin-bottom: 4.6rem;

    @include mobile {
      margin-bottom: 2rem;
    }
  }

  &__img {
    max-height: 6.5rem;

    @include mobile {
      max-height: 6rem;
    }
  }

  &__header {
    font-size: 1.3rem;
    line-height: 1.5rem;
    font-weight: bold;
    color: $primaryText;
    transition: color .3s;

    @include mobile {
      font-size: 1rem;
      line-height: 1.3rem;
    }

    #{$self}__li.active & {
      color: $white;
    }

    #{$self}__item:hover & {
      @include fromDesktop {
        color: $green;
      }
    }
  }

  &__icon {
    font-size: 6.5rem;
    color: #2175AA;

    [data-class="card-icon"] {
      fill: #fff;
    }

    #{$self}__li.active & {
      color: $white;

      [data-class="card-icon"] {
        fill: #2175AA;
      }
    }
  }
}
