.head-news {
  background: $darkBlue;
  padding: 5.4rem 0 6rem;

  &__container {
    max-width: 123rem;
  }

  &__head-col {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  &__header {
    font-size: 5rem;
    line-height: 6rem;
    color: $white;
    font-weight: bold;

    @include lessVerticalTablet {
      font-size: $mobileMainHeader;
    }
  }

  &__select-col {
    margin-left: auto;

    @include lessVerticalTablet {
      width: 100%;
    }
  }

  &__select-list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: -.6rem;
  }

  &__select-li {
    padding: .6rem;
  }

  &__select-year {
    .select {
      &__btn {
        min-width: 14.4rem;
      }
    }
  }
}
