.contact {
  background: $white;
  padding-bottom: 14.6rem;

  @include lessVerticalTablet {
    padding-bottom: $mobileVerticalArea;
  }

  &__head {
    background: $primaryText;
    padding-bottom: 20.6rem;
    padding-top: 5.4rem;

    @include lessVerticalTablet {
      padding-bottom: 15rem;
    }
  }

  &__header {
    font-size: 5rem;
    line-height: 6rem;
    color: $white;
    font-weight: bold;
    margin-bottom: 7.2rem;

    @include lessVerticalTablet {
      font-size: $mobileMainHeader;
      margin-bottom: 0;
    }
  }

  &__list-col {

  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: -7.35rem;

    @include lessVerticalTablet {
      margin: 0;
    }
  }

  &__li {
    padding: 7.35rem;

    @include lessVerticalTablet {
      width: 100%;
      padding: 2rem 0;
    }
  }

  &__item {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
  }

  &__title {
    font-size: 2rem;
    line-height: 3rem;
    font-weight: bold;
    color: $white;
    margin-top: -.5rem;
    margin-right: 5rem;

    @include mobile {
      margin-right: auto;
    }

    @include lessVerticalTablet {
      font-size: $mobileSecondHeader;
    }
  }

  &__item-list {
    margin: -.5rem;
  }

  &__item-li {
    padding: .5rem;
  }

  &__item-col {
    display: flex;
    flex-wrap: wrap;
    font-size: 2rem;
    line-height: 2.4rem;
    color: $white;
  }

  &__key {
    color: rgba($white, .7);
    margin-right: .5rem;

    @include lessVerticalTablet {
      font-size: $mobileText;
    }
  }

  &__value {
    @include lessVerticalTablet {
      font-size: $mobileText;
    }
  }

  &__contact-form {
    margin-top: -13.8rem;
    margin-bottom: 14.3rem;

    @include lessVerticalTablet {
      margin-bottom: 6.4rem;
    }
  }
}
